import React, { useState, useEffect, useRef } from 'react';
import { useSearchParams } from 'react-router-dom'
import { useTranslation } from "react-i18next";
import { Tooltip, Col, Row, Select, message, Modal, Form, Input, Button, Checkbox, Popover } from 'antd';
import './Manage.css'
import { getCommon } from '@/api/common'
import * as manage from '@/api/cloudManage'
import * as cloud from "@/api/cloud";
import { genEnCode, showDialog } from '@/utils/util.js'
import DelDialog from '@/components/delDialog/DelDialog';
import {
  QuestionCircleOutlined
} from '@ant-design/icons';
import PayModal from "@/components/payModal/PayModal";
const { Option } = Select;
export default function Manage (props) {
  const [form] = Form.useForm()
  const [pasForm] = Form.useForm()
  const [reForm] = Form.useForm() // 
  const optOs = Form.useWatch('optOs', reForm) || { type: 'os', group: '1' }
  const { t } = useTranslation()
  const [search] = useSearchParams()
  const cloudDetail = props.detail
  const id = search.get('id') // id
  /*  */
  const [common, setCommon] = useState()
  const [type, setType] = useState(() => { // 
    return props.type === 'on' ? 'off' : 'on'
  })
  const [detail, setDetail] = useState({})
  const [optTitle, setOptTitle] = useState('')
  const [des, setDes] = useState('')
  const [optVisible, setOptVisible] = useState(false)
  const [pasVisible, setPasVisible] = useState(false)
  const [reinstallVisible, setReinstallVisible] = useState(false)
  // /
  const [showQuit, setShowQuit] = useState(false)
  const [optType, setOptType] = useState(null)
  /*  */
  // 
  const [imageList, setImageList] = useState([])
  // 
  const [template, setTemplate] = useState([])
  //  
  const [choose, setChoose] = useState('os') // /
  const [group, setGroup] = useState()
  const [curList, setCurList] = useState([])
  const [pasway, setPasway] = useState('password') // /ssh
  const [needPay, setNeedPay] = useState(false) // 
  const [price, setPrice] = useState(0.00)
  const [ssh, setSsh] = useState([])
  const [pasType, setPasType] = useState('password')
  const [orderId, setOrderId] = useState()
  // 
  let timer = useRef()
  // 
  const [time, setTime] = useState(300000); // 5
  const [isShowPay, setIsShowPay] = useState(false)
  // 
  const [pasData, setPasData] = useState({
    id,
    password: ''
  })
  /* api */
  const getHost = async () => {
    try {
      const res = await manage.getProductDetail(id)
      const data = res.data.data.host
      setDetail(data)
      form.setFieldsValue({
        host_name: data.name
      })
    } catch (error) {
    }
  }
  // 
  const getCommonConfig = async () => {
    try {
      const res = await getCommon()
      setCommon(res.data.data)
    } catch (error) {
    }
  }
  // URL
  const postVnc = async () => {
    try {
      const res = await manage.postVnc(id)
      window.open(res.data.data.url)
    } catch (error) {

    }
  }
  // 
  const getImage = async () => {
    try {
      const res = await manage.getSystemImage({
        product_id: 30,
        data_center_id: props.detail.data_center.id
      })
      setImageList(res.data.data.list)
      setCurList(res.data.data.list[0].image)
      reForm.setFieldsValue({
        optOs: {
          group: res.data.data.list[0]?.id,
          id: res.data.data.list[0].image[0]?.id
        }
      })
    } catch (error) {
    }
  }
  // 
  const getTemplate = async () => {
    try {
      const res = await manage.getTemplate(id)
      setTemplate(res.data.data.list)
    } catch (error) {
      console.log(error)
    }
  }
  // ssh
  const getSsh = async () => {
    try {
      const res = await manage.getSsh({
        page: 1,
        limit: 1000
      })
      setSsh(res.data.data.list)
    } catch (error) {
      console.log(error)
    }
  }
  useEffect(() => {
    getCommonConfig()
    getImage()
    getTemplate()
    return () => clearInterval(timer.current)
  }, [])
  useEffect(() => {
    getHost()
    setType(props.type === 'on' ? 'off' : 'on')
  }, [props.type])
  /*  */
  const handleChange = e => {
    setType(e)
  }
  const handelPower = () => {
    setOptVisible(true)
    form.resetFields()
    switch (type) {
      case 'on':
        return setOptTitle(t('cloud.tip16'))
      case 'off':
        return setOptTitle(t('cloud.tip17'))
      case 'restart':
        return setOptTitle(t('cloud.tip18'))
      default:
        return setOptTitle('title')
    }
  }
  const optCancel = () => {
    setOptVisible(false)
    setPasVisible(false)
    setReinstallVisible(false)
  }
  // 
  const refresh = () => {
    props.refresh()
  }
  // 
  const showDia = (msg, status) => {
    showDialog(msg, status, () => {
      optCancel()
    })
  }
  // 
  const powerOn = async () => {
    try {
      const res = await manage.powerOn(id)
      if (res.data.status === 200) {
        refresh()
        showDia(res.data.msg, 'success')
      }
    } catch (error) {
      showDia(error.data.msg, 'error')
    }
  }
  // 
  const powerOff = async () => {
    try {
      const force = form.getFieldsValue().remember
      const res = await manage.powerOff(force, id)
      if (res.data.status === 200) {
        refresh()
        showDia(res.data.msg, 'success')
      }
    } catch (error) {
      showDia(error.data.msg, 'error')
    }
  }
  // 
  const reboot = async () => {
    try {
      const force = form.getFieldsValue().remember
      const res = await manage.reboot(force, id)
      if (res.data.status === 200) {
        refresh()
        showDia(res.data.msg, 'success')
      }
    } catch (error) {
      showDia(error.data.msg, 'error')
    }
  }
  // 
  const onFinish = () => {
    switch (type) {
      case 'on':
        return powerOn()
      case 'off':
        return powerOff()
      case 'restart':
        return reboot()
      default:
        break;
    }
  }
  // |
  const handlerPas = (type) => {
    // 
    if (type === 'rescue' && Number(props.isEscue)) {
      setShowQuit(true)
      return
    }
    setPasVisible(true)
    setOptType(type)
    pasForm.resetFields()
    if (type === 'password') {
      setOptTitle(t("cloud.reset_password"))
      setDes(t("cloud.tip22"))
    } else if (type === 'rescue') {
      setOptTitle(t("cloud.escue_system"))
      setDes(t("cloud.tip27"))
    }
    createPassword()
  }
  // 
  const closeDialog = () => {
    setShowQuit(false)
  }
  const submitDel = async () => {
    try {
      const res = await manage.exitRescue(id)
      if (res.data.status === 200) {
        showDia(res.data.msg, 'success')
        refresh()
      }
    } catch (error) {
      showDia(error.data.msg, 'error')
    }
  }
  // 
  const changeSystem = () => {

  }
  // 
  const createPassword = (type) => {
    const password = genEnCode(10, 1, 1, 0, 1, undefined, 1)
    const first = [String.fromCharCode(Math.floor(Math.random() * 25 + 65)), String.fromCharCode(Math.floor(Math.random() * 25 + 97))]
    const f1 = first[Math.round(Math.random())] + Math.floor(Math.random() * 10)
    if (type === 'reinstall') { // 
      reForm.setFieldsValue({
        pas: {
          input: password + f1
        }
      })
      return
    }
    // 
    pasForm.setFieldsValue({
      password: password + f1
    })
    setPasData(pasData => ({ ...pasData, password: password + f1 }))
  }
  // 
  const surePassword = async (values) => {
    const params = {
      id,
      password: values.password,
      type: values.type === 'Windows' ? 1 : 2
    }
    if (optType === 'password') {
      delete params.type
    }
    try {
      if (optType === 'password') { // 
        const res = await manage.resetPassword(params)
        if (res.data.status === 200) {
          showDia(res.data.msg, 'success')
          refresh()
        }
      } else if (optType === 'rescue') { // 
        const res = await manage.rescue(params)
        if (res.data.status === 200) {
          showDia(res.data.msg, 'success')
          refresh()
        }
      }
    } catch (error) {
      return showDia(error.data.msg, 'error')
    }
  }
  // 
  const handlerReinstall = () => {
    setReinstallVisible(true)
    setOptTitle(t("cloud.reinstall"))
    getSsh()
    // 
    createPassword('reinstall')
    setChoose('os')
    setCurList(imageList[0]?.image)
    setPasType('password')
    reForm.setFieldsValue({
      optOs: {
        type: 'os',
        group: imageList[0]?.id,
        id: imageList[0]?.image[0]?.id
      },
      pas: {
        type: 'password'
      },
      port: {
        num: 22
      }
    })
    imageIsBuy(imageList[0]?.image[0]?.id)
  }
  //  /
  const changeOs = (e) => {
    setChoose(e)
    reForm.setFieldsValue({
      optOs: {
        group: e === 'os' ? imageList[0]?.id : template[0]?.id,
        id: e === 'os' ? imageList[0]?.image[0]?.id : template[0]?.id
      }
    })
    setCurList(curList => {
      curList = imageList[0].image
      return curList
    })
    if (e === 'os' && imageList[0].image[0].id) {
      imageIsBuy(imageList[0].image[0].id)
    }
  }
  // 
  const changeGroup = (e) => {
    setGroup(e)
    const arr = imageList.filter(item => item.id === e)
    setCurList(curList => {
      curList = arr ? arr[0].image : []
      return curList
    })
    const id = arr ? arr[0].image[0].id : ''
    reForm.setFieldsValue({
      optOs: {
        id
      }
    })
    imageIsBuy(id)
  }
  // 
  const changeImage = (e) => {
    if (choose === 'os') {
      imageIsBuy(e)
    }
  }
  // 
  const imageIsBuy = async (image_id) => {
    try {
      const params = {
        id,
        image_id
      }
      const res = await manage.imageIsBuy(params)
      if (res.data.status === 200) {
        setNeedPay(res.data.data.price * 1 > 0 ? true : false)
        setPrice(res.data.data.price)
      }
    } catch (error) {
    }
  }
  // 
  const randomPort = () => {
    const start = 10000
    const end = 20000
    const differ = end - start
    const random = Math.random()
    const port = (start + differ * random).toFixed(0)
    reForm.setFieldsValue({
      port: {
        num: port
      }
    })
  }
  // password/ssh
  const changeTye = (type) => {
    setPasType(type)
    if (type === 'password') {
      createPassword('reinstall')
    } else {
      reForm.setFieldsValue({
        pas: {
          input: ssh[0].id || ''
        }
      })
    }
  }
  // 
  const reinstallFinish = (values) => {
    if (needPay) {
      return
    }
    const params = {
      id,
      image_id: values.optOs.id,
      template_id: values.optOs.id,
      password: values.pas.input,
      ssh_key_id: values.pas.input,
      port: values.port.num
    }
    if (values.optOs.type === 'os') {
      delete params.template_id
    } else {
      delete params.image_id
    }
    if (values.pas.type === 'password') {
      delete params.ssh_key_id
    } else {
      delete params.password
    }
    reinstallOpt(params)
  }
  // 
  const reinstallOpt = async (params) => {
    try {
      const res = await manage.reinstall(params)
      if (res.data.status === 200) {
        refresh()
        showDia(res.data.msg, 'success')
      }
    } catch (error) {
      showDia(error.data.msg, 'error')
    }
  }
  // 
  const createOrder = async () => {
    const params = {
      id,
      image_id: reForm.getFieldsValue().optOs.id
    }
    const res = await manage.createOrder(params)
    if (res.data.status === 200) {
      const order_id = res.data.data.id
      setIsShowPay(true)
      setOrderId(order_id)
      // 
      pollingStatus(order_id);
    }
  }
  // 
  const pollingStatus = (id) => {
    timer.current = setInterval(async () => {
      const res = await cloud.getPayStatus(id ? id : orderId);
      setTime((time) => {
        return (time -= 2000);
      });
      if (res.data.code === "Paid") {
        showDialog(res.data.msg, 'success', () => {
          setNeedPay(false)
        })
        clearInterval(timer.current);
        setIsShowPay(false);
        return false;
      }
      // 5
      if (res.code === "Paid" || time === 0) {
        clearInterval(timer.current);
        setIsShowPay(false);
        return false;
      }
    }, 2000)
  };
  // 
  const handleCancel = () => {
    setIsShowPay(false)
    clearInterval(timer.current);
  }
  const onFinishFailed = () => {

  }
  return (
    <div className='manage'>
      <Row gutter={[{ xs: 8, sm: 16, md: 24, lg: 30 }, { xs: 8, sm: 16, md: 24, lg: 30 }]}>
        <Col xs={{ span: 24 }} md={{ span: 12 }} lg={{ span: 8 }}>
          <div className="item">
            <div className="icon">
              <img src={require('@/assets/images/cloud/m1.png')} alt="" />
            </div>
            <div className="text">
              <p className="tit">{t("cloud.power")}</p>
              <Select onChange={handleChange} value={type} getPopupContainer={triggerNode => triggerNode.parentElement}>
                {
                  props.detail.power_status !== 'on' && <Option value="on">{t("cloud.on")}</Option>
                }
                {
                  props.detail.power_status !== 'off' && <Option value="off">{t("cloud.off")}</Option>
                }
                <Option value="restart">{t("cloud.restart")}</Option>
              </Select>
              <div className="c-btn" onClick={handelPower}>{t("common.sure")}</div>
            </div>
          </div>
        </Col>
        <Col xs={{ span: 24 }} md={{ span: 12 }} lg={{ span: 8 }}>
          <div className="item">
            <div className="icon">
              <img src={require('@/assets/images/cloud/m2.png')} alt="" />
            </div>
            <div className="text">
              <p className="tit">{t("cloud.console")}</p>
              <p className="des">
                {t("cloud.tip7")}
              </p>
              <div className="c-btn" onClick={postVnc}>{t("cloud.go_now")}</div>
            </div>
          </div>
        </Col>
        <Col xs={{ span: 24 }} md={{ span: 12 }} lg={{ span: 8 }}>
          <div className="item">
            <div className="icon">
              <img src={require('@/assets/images/cloud/m3.png')} alt="" />
            </div>
            <div className="text">
              <p className="tit">{t("cloud.reset_password")}</p>
              <p className="des">
                {t("cloud.tip8")}
              </p>
              <div className="c-btn" onClick={() => handlerPas('password')}>{t("cloud.go_now")}</div>
            </div>
          </div>
        </Col>
        <Col xs={{ span: 24 }} md={{ span: 12 }} lg={{ span: 8 }}>
          <div className="item">
            <div className="icon">
              <img src={require('@/assets/images/cloud/m4.png')} alt="" />
            </div>
            <div className="text">
              <p className="tit">{t("cloud.escue_mode")}</p>
              <p className="des">
                {t("cloud.tip9")}
              </p>
              <div className="c-btn" onClick={() => handlerPas('rescue')}>{Number(props.isEscue) ? t("common.quit") : t("common.enter")}{t("cloud.escue_mode")}</div>
            </div>
          </div>
        </Col>
        <Col xs={{ span: 24 }} md={{ span: 12 }} lg={{ span: 8 }}>
          <div className="item">
            <div className="icon">
              <img src={require('@/assets/images/cloud/m5.png')} alt="" />
            </div>
            <div className="text">
              <p className="tit">{t("cloud.reinstall")}</p>
              <p className="des">
                {t("cloud.tip10")}
              </p>
              <div className="c-btn" onClick={handlerReinstall}>{t("cloud.go_now")}</div>
            </div>
          </div>
        </Col>
        <Col xs={{ span: 24 }} md={{ span: 12 }} lg={{ span: 8 }}>
          <div className="item">
            <div className="icon">
              <img src={require('@/assets/images/cloud/m6.png')} alt="" />
            </div>
            <div className="text">
              <p className="tit">{t("cloud.mount_iso")}</p>
              <p className="des">
                {t("cloud.tip11")}
              </p>
              <div className="c-btn">{t("cloud.go_now")}</div>
            </div>
          </div>
        </Col>
        <Col xs={{ span: 24 }} md={{ span: 12 }} lg={{ span: 8 }}>
          <div className="item">
            <div className="icon">
              <img src={require('@/assets/images/cloud/m7.png')} alt="" />
            </div>
            <div className="text">
              <p className="tit">{t("cloud.startup")}</p>
              <p className="des">
                {t("cloud.tip12")}
              </p>
              <div className="c-btn">{t("cloud.go_now")}</div>
            </div>
          </div>
        </Col>
        <Col xs={{ span: 24 }} md={{ span: 12 }} lg={{ span: 8 }}>
          <div className="item">
            <div className="icon">
              <img src={require('@/assets/images/cloud/m8.png')} alt="" />
            </div>
            <div className="text">
              <p className="tit">{t("cloud.migrate")}</p>
              <p className="des">
                {t("cloud.tip13")}
              </p>
              <div className="c-btn">{t("cloud.go_now")}</div>
            </div>
          </div>
        </Col>
        <Col xs={{ span: 24 }} md={{ span: 12 }} lg={{ span: 8 }}>
          <div className="item">
            <div className="icon">
              <img src={require('@/assets/images/cloud/m1.png')} alt="" />
            </div>
            <div className="text">
              <p className="tit">{t("cloud.delete_instance")}</p>
              <p className="des">
                {t("cloud.tip14")}
              </p>
              <div className="c-btn">{t("cloud.go_now")}</div>
            </div>
          </div>
        </Col>
      </Row>

      {/*  */}
      <Modal title={optTitle} forceRender
        visible={optVisible} onCancel={optCancel}
        footer={null} className="power-modal" closable={false}>
        {
          detail && (
            <Form form={form}
              initialValues={{
                host_name: detail.name,
                remember: false,
              }}
              onFinish={onFinish}
              onFinishFailed={onFinishFailed}
              autoComplete="off"
            >
              <Form.Item label={t("cloud.host_name")} colon={false} name="host_name">
                <Input disabled />
              </Form.Item>
              {
                type !== 'on' && (
                  <>
                    <p className='tip'>{t("cloud.tip19")}</p>
                    <Form.Item colon={false}
                      name="remember"
                      valuePropName="checked"
                      // rules={[
                      //   {
                      //     validator: (_, value) =>
                      //       value ? Promise.resolve() : Promise.reject(new Error(t("cloud.tip21"))),
                      //   },
                      // ]} 
                      className="force-off">
                      <Checkbox>{t("cloud.tip20")}</Checkbox>
                    </Form.Item>
                  </>
                )
              }
              <Form.Item className="opt-btn">
                <Button type="primary" htmlType="submit" className="sure">
                  {t("common.sure")}
                </Button>
                <Button onClick={optCancel} className="cancel">
                  {t("common.cancel")}
                </Button>
              </Form.Item>
            </Form>
          )
        }
      </Modal>
      {/* | */}
      <Modal title={optTitle} forceRender
        visible={pasVisible} onCancel={optCancel}
        footer={null} className="power-modal password-modal" closable={false}>
        <Form form={pasForm}
          labelAlign='left'
          initialValues={{
            password: pasData.password,
            type: props.detail && props.detail.image.name.toLocaleLowerCase().indexOf('win') !== -1 ? 'Windows' : 'Linux'
          }}
          onFinish={surePassword}
          onFinishFailed={onFinishFailed}
          autoComplete="off"
          className={{ 'rescue-form': optType === 'rescue' }}
        >
          <p className="small-tip">{des}</p>
          {
            optType === 'rescue' ? (
              <Form.Item label={t("cloud.escue_system_type")} colon={false} name="type"
              >
                <Select
                  getPopupContainer={triggerNode => triggerNode.parentElement}
                  onChange={changeSystem}
                >
                  <Option value="Windows">
                    <img src={require('@/assets/images/cloud/Windows.png')} alt="" className="sys-icon" />
                    Windows
                  </Option>
                  <Option value="Linux">
                    <img src={require('@/assets/images/cloud/Linux.png')} alt="" className="sys-icon" />
                    Linux
                  </Option>
                </Select>
              </Form.Item>
            ) : ''
          }
          <div className="pas-box">
            <Form.Item label={optType === 'password' ? t("cloud.password") : t("cloud.temp_password")}
              required tooltip={
                t("cloud.verify4") + '\n' +
                t("cloud.verify5") +
                t("cloud.verify6") +
                t("cloud.verify7")
              }
              colon={false} name="password"
            >
              <Input></Input>
            </Form.Item>
            <div className="c-btn" onClick={createPassword}>{t("cloud.random_generate")}</div>
          </div>
          {/*  */}
          {
            props.type !== 'off' && optType === 'password' &&
            (
              <>
                <div className="com-danger">
                  <p className="tit">{t("cloud.tip23")}</p>
                  <div className="des">
                    <p>{t("cloud.tip24")}</p>
                    <p>{t("cloud.tip25")}</p>
                  </div>
                </div>
                <Form.Item colon={false}
                  required name="force"
                  label={
                    t("cloud.force_shutdown")
                  }
                  valuePropName="checked" rules={[
                    {
                      validator: (_, value) =>
                        value ? Promise.resolve() : Promise.reject(new Error(t("cloud.tip26"))),
                    },
                  ]} className="force-off">
                  <Checkbox>{t("cloud.agree_force_shutdown")}</Checkbox>
                </Form.Item>
              </>
            )
          }
          {/*  */}
          {
            optType === 'rescue' ? (
              <div className="com-danger">
                <p className="tit">{t("cloud.tip28")}</p>
              </div>
            ) : ''
          }
          <Form.Item className="opt-btn">
            <Button type="primary" htmlType="submit" className="sure">
              {t("common.sure")}
            </Button>
            <Button onClick={optCancel} className="cancel">
              {t("common.cancel")}
            </Button>
          </Form.Item>
        </Form>
      </Modal>
      {/*  */}
      <Modal title={optTitle} forceRender
        visible={reinstallVisible} onCancel={optCancel}
        footer={null} className="power-modal reinstall-modal" closable={false}>
        {
          detail && (
            <Form form={reForm}
              initialValues={{
                optOs: {
                  type: choose
                },
                pas: {
                  type: 'password'
                }
              }}
              onFinish={reinstallFinish}
              onFinishFailed={onFinishFailed}
              autoComplete="off"
            >
              {/*  */}
              <Form.Item required label=" " colon={false}>
                <Input.Group compact>
                  <Form.Item
                    name={['optOs', 'type']}
                    noStyle
                  >
                    <Select className="re-type" onChange={changeOs}>
                      <Option value="os">{t("cloud.operating_system")}</Option>
                      <Option value="template">{t("cloud.template")}</Option>
                    </Select>
                  </Form.Item>
                  <Form.Item
                    name={['optOs', 'group']}
                    noStyle
                  >
                    {
                      choose === 'os' ? (
                        <Select className="system" getPopupContainer={triggerNode => triggerNode.parentElement}
                          onChange={changeGroup}>
                          {
                            imageList.map(item => {
                              return (<Option value={item.id} key={item.id}>
                                <img src={item.name && require(`@/assets/images/cloud/${item.name}.png`)} alt="" className="sys-icon" />
                                {item.name}
                              </Option>)
                            })
                          }
                        </Select>) : (
                        <Select className="system dis-none" getPopupContainer={triggerNode => triggerNode.parentElement}>
                          {
                            template.map(item => {
                              return (<Option value={item.id} key={item.id}>
                                {item.name}
                              </Option>)
                            })
                          }
                        </Select>
                      )
                    }
                  </Form.Item>
                  <Form.Item name={['optOs', 'id']}>
                    {
                      choose === 'os' ? <Select className="image-id" onChange={changeImage} >
                        {
                          curList.map(item => {
                            return (<Option value={item.id} key={item.id}>{item.name}</Option>)
                          })
                        }
                      </Select> : <Select className="system" getPopupContainer={triggerNode => triggerNode.parentElement}>
                        {
                          template.map(item => {
                            return (<Option value={item.id} key={item.id}>
                              {item.name}
                            </Option>)
                          })
                        }
                      </Select>
                    }
                  </Form.Item>
                </Input.Group>
              </Form.Item>
              {/* /ssh */}
              <Form.Item required label=" " colon={false}>
                <Input.Group compact>
                  <Form.Item
                    name={['pas', 'type']}
                    required noStyle>
                    <Select className="re-type" onChange={changeTye}>
                      <Option value="password">{t("cloud.password")}</Option>
                      <Option value="ssh">{t("account.ssh")}</Option>
                    </Select>
                  </Form.Item>
                  {
                    pasType === 'password' ? (
                      <>
                        <Tooltip title={
                          t("cloud.verify4") + '\n' +
                          t("cloud.verify5") +
                          t("cloud.verify6") +
                          t("cloud.verify7")
                        }>
                          <QuestionCircleOutlined className="question" />
                        </Tooltip>
                        <Form.Item
                          name={['pas', 'input']}
                          noStyle
                        >
                          <Input className="pas-select"></Input>
                        </Form.Item>
                        <Form.Item>
                          <div className="c-btn" onClick={() => createPassword('reinstall')}>
                            {t("cloud.random_generate")}
                          </div>
                        </Form.Item>
                      </>
                    ) : (
                      <>
                        <Tooltip title={
                          t("cloud.verify8")
                        }>
                          <QuestionCircleOutlined className="question" />
                        </Tooltip>
                        <Form.Item
                          name={['pas', 'input']}
                          noStyle
                          rules={[
                            {
                              required: true,
                              message: t('select') + t('account.ssh')
                            }
                          ]}
                        >
                          <Select className="pas-select">
                            {
                              ssh.map(item => {
                                return <Option value={item.id} key={item.id}>{item.name}</Option>
                              })
                            }
                          </Select>
                        </Form.Item>
                      </>
                    )
                  }
                </Input.Group>
              </Form.Item>
              {/*  */}
              <Form.Item required label={t("common.port")} colon={false}>
                <Input.Group compact>
                  <Form.Item
                    name={['port', 'num']}
                    noStyle
                    rules={[
                      {
                        required: true,
                        message: t('cloud.verify9'),
                        validator (_, value) {
                          value = parseInt(value)
                          if (value === 22 || (value >= 100 && value <= 65535)) {
                            return Promise.resolve()
                          } else {
                            return Promise.reject(t('cloud.verify9'))
                          }
                        }
                      }
                    ]}
                  >
                    <Input></Input>
                  </Form.Item>
                  <Form.Item
                    noStyle
                  >
                    <div className="c-btn" onClick={randomPort}>
                      {t("cloud.random_generate")}
                    </div>
                  </Form.Item>
                </Input.Group>
              </Form.Item>
              {/*  */}
              {
                needPay && <div className="pay-box">
                  {t("cloud.tip29")}{common.currency_prefix}{price}/{t("common.once")} {t("cloud.tip29")}
                  <div className="go-pay" onClick={createOrder}>
                    {t("common.buy_now")}
                  </div>
                </div>
              }
              <Form.Item className="opt-btn">
                <Button type="primary" htmlType="submit" className={{ sure: true, disabled: needPay }} disabled={needPay}>
                  {t("common.sure")}
                </Button>
                <Button onClick={optCancel} className="cancel">
                  {t("common.cancel")}
                </Button>
              </Form.Item>
            </Form>
          )
        }
      </Modal>
      {/* modal */}
      {
        orderId &&
        (
          <PayModal orderId={orderId} timer={timer} visible={isShowPay} handleCancel={handleCancel}></PayModal>
        )
      }
      {/*  */}
      {
        showQuit && <DelDialog
          closeDialog={closeDialog} submitDel={submitDel}
          title=' '
          icon="warning"
          text={
            t('common.sure') + t('common.quit') + t('cloud.escue_mode') + '?'
          }
        ></DelDialog>
      }
    </div >
  )
}
