import React, { useState, useEffect } from 'react'
import ComPagination from '@/components/comPagination/ComPagination.jsx'
import { Table, Button, Modal, Row, Col, Input, Select, Alert } from 'antd';
import moment from 'moment';
import Swal from "sweetalert2";
import './BackupSnapshot.less'

import { snapshot, backup, delSnapshot, restoreSnapshot, restoreBackup, delBackup, getDiskById, createSnapshot, mount } from "@/api/cloud"
import { showDialog } from '@/utils/util';
import { useTranslation } from "react-i18next";
import { createBackup } from '../../../../api/cloud';

export default function BackupSnapshot() {
  const { Option } = Select;
  const { t } = useTranslation();
  // id
  const [id, setId] = useState(
    () => Number(window.location.hash.split("=")[1])
  )

  // 
  const [errMsg, setErrMsg] = useState("")

  //     true:   false: 
  const [bOrs, setBOrs] = useState(true)

  // /
  // 
  const [isSelectLoading, setIsSelectLoading] = useState(false)
  const [sForm, setSForm] = useState({
    id,
    name: null,
    disk_id: null
  })
  const [isSmodalVisible, setIsSmodalVisible] = useState(false)

  const [diskList, setDiskList] = useState([])
  // 
  const [total, setTotal] = useState(0)
  const [params, setParams] = useState({
    keywords: '',
    page: 1,
    limit: 5,
    orderby: 'id',
    sort: 'desc'
  })

  // 
  const [backTotal, setBackTotal] = useState(10)
  const [backParams, setBackParams] = useState({
    keywords: '',
    page: 1,
    limit: 5,
    orderby: 'id',
    sort: 'desc'
  })

  useEffect(() => {
    getSnapshot()
  }, [params]);
  useEffect(() => {
    getBackup()
  }, [backParams])


  // 
  const [isTableLoading, setIsTableLoading] = useState(false)
  const [datasource, setDatasource] = useState([])
  const changePage = (page, limit) => {
    setParams(params => {
      return { ...params, page, limit }
    })
  }

  // 
  const [isBackTableLoading, setIsBackTableLoading] = useState(false)
  const [backDatasource, setBackDatasource] = useState([])
  const changeBackPage = (page, limit) => {
    setBackParams(params => {
      return { ...params, page, limit }
    })
  }

  // 
  const [isSnapshot, setIsSnapshot] = useState(false)
  // 
  const [isBackup, setIsBackup] = useState(false)

  //  columns
  const columns = [
    {
      title: t('cloud.bas_columns_title1'),
      dataIndex: 'name',
      ellipsis: true,
      width: 300,
    },
    {
      title: t('cloud.bas_columns_title2'),
      dataIndex: 'notes',
      ellipsis: true,
    },
    {
      title: t('cloud.bas_columns_title3'),
      dataIndex: 'create_time',
      width: 200,
      render: (create_time) => (
        <>
          {
            create_time ?
              moment(create_time * 1000).format("YYYY-MM-DD HH:mm")
              :
              "--"
          }
        </>
      )
    },
    {
      title: t('cloud.bas_columns_title4'),
      width: 80,
      render: (_, record) => {
        return (
          <div className='more'>
            <span className='more-text'>...</span>
            <span className='more-operation'>
              <a className='more-dilatation' onClick={() => doRestoreSnapshot(record)} >{t('cloud.bas_btn5')}</a>
              <a className='more-delete' onClick={() => deleteSnapshot(record)} >{t('cloud.bas_btn6')}</a>
            </span>
          </div>
        )
      }
    }
  ]

  //  columns
  const backColumns = [
    {
      title: t('cloud.bas_columns_title1'),
      dataIndex: 'name',
      ellipsis: true,
      width: 300,
    },
    {
      title: t('cloud.bas_columns_title2'),
      dataIndex: 'notes',
      ellipsis: true,
    },
    {
      title: t('cloud.bas_columns_title3'),
      dataIndex: 'create_time',
      width: 200,
      render: (create_time) => (
        <>
          {
            create_time ?
              moment(create_time * 1000).format("YYYY-MM-DD HH:mm")
              :
              "--"
          }
        </>
      )
    },
    {
      title: t('cloud.bas_columns_title4'),
      width: 80,
      render: (_, record) => {
        return (
          <div className='more'>
            <span className='more-text'>...</span>
            <span className='more-operation'>
              <a className='more-dilatation' onClick={() => doRestoreBackup(record)} >{t('cloud.bas_btn5')}</a>
              <a className='more-delete' onClick={() => deleteBackup(record)} >{t('cloud.bas_btn6')}</a>
            </span>
          </div>
        )
      }
    }
  ]

  // 
  const getSnapshot = () => {
    setIsTableLoading(true)
    // const id = window.location.hash.split("=")[1]
    snapshot({ ...params, id }).then(res => {
      if (res.data.status === 200 && res.data.data.count > 0) {
        setTotal(res.data.data.count)
        setIsSnapshot(true)
        const list = res.data.data.list
        console.log(res);
        setDatasource(list)
      } else {
        setIsSnapshot(false)
      }
      setIsTableLoading(false)
    })
  }

  // 
  const getBackup = () => {
    setIsBackTableLoading(true)
    // const id = window.location.hash.split("=")[1]
    backup({ ...params, id }).then(res => {
      if (res.data.status === 200 && res.data.data.count > 0) {
        setBackTotal(res.data.data.count)
        setIsBackup(true)
        const list = res.data.data.list
        setBackDatasource(list)
      } else {
        setIsBackup(false)
      }
      setIsBackTableLoading(false)
    })
  }

  // 
  const deleteSnapshot = (record) => {
    Swal.fire({
      text: ` ${record.name},`,
      icon: 'info',
      showCancelButton: true,
      confirmButtonColor: '#5C5EE4',
      confirmButtonText: '',
      cancelButtonText: '',
      customClass: {
        icon: 'ssh-icon',
        cancelButton: 'ssh-cancel-button',
      }
    }).then((result) => {
      if (result.value) {
        // 
        const params = {
          id,
          snapshot_id: record.id
        }
        delSnapshot(params).then(res => {
          if (res.data.status === 200) {
            showDialog(` ${record.name}`, "success")
            getSnapshot()
          }
        })
      }
    })
  }

  // 
  const doRestoreSnapshot = (record) => {
    Swal.fire({
      text: ` ${record.name},`,
      icon: 'info',
      showCancelButton: true,
      confirmButtonColor: '#5C5EE4',
      confirmButtonText: '',
      cancelButtonText: '',
      customClass: {
        icon: 'ssh-icon',
        cancelButton: 'ssh-cancel-button',
      }
    }).then((result) => {
      if (result.value) {
        // 
        const params = {
          id,
          snapshot_id: record.id
        }
        restoreSnapshot(params).then(res => {
          if (res.data.status === 200) {
            showDialog(` ${record.name}`, "success")
            getSnapshot()
          }
        })
      }
    })
  }

  // 
  const doRestoreBackup = (record) => {
    Swal.fire({
      text: ` ${record.name},`,
      icon: 'info',
      showCancelButton: true,
      confirmButtonColor: '#5C5EE4',
      confirmButtonText: '',
      cancelButtonText: '',
      customClass: {
        icon: 'ssh-icon',
        cancelButton: 'ssh-cancel-button',
      }
    }).then((result) => {
      if (result.value) {
        // 
        const params = {
          id,
          snapshot_id: record.id
        }
        restoreBackup(params).then(res => {
          if (res.data.status === 200) {
            showDialog(` ${record.name}`, "success")
            getBackup()
          }
        })
      }
    })
  }

  // 
  const deleteBackup = (record) => {
    Swal.fire({
      text: ` ${record.name},`,
      icon: 'info',
      showCancelButton: true,
      confirmButtonColor: '#5C5EE4',
      confirmButtonText: '',
      cancelButtonText: '',
      customClass: {
        icon: 'ssh-icon',
        cancelButton: 'ssh-cancel-button',
      }
    }).then((result) => {
      if (result.value) {
        // 
        const params = {
          id,
          snapshot_id: record.id
        }
        delBackup(params).then(res => {
          if (res.data.status === 200) {
            showDialog(` ${record.name}`, "success")
            getBackup()
          }
        })
      }
    })
  }

  // id
  const showDisk = (open) => {
    if (open) {
      setIsSelectLoading(true)
      getDiskById(id).then(res => {
        if (res.data.list) {
          setDiskList(res.data.list)
        }
        setIsSelectLoading(false)
      })
    }
  }

  const getDiskIdValue = (e) => {
    setSForm({
      ...sForm,
      disk_id: Number(e)
    });
  };

  const getNameValue = (e) => {
    setSForm({
      ...sForm,
      name: e.target.value,
    })
  }

  // /
  const doCreateSnapshot = () => {
    let isPass = true;
    if (sForm.name) {
      if (sForm.name.length > 10) {
        setErrMsg("10")
        isPass = false
      }
    } else {
      setErrMsg("")
      isPass = false
    }

    if (!sForm.disk_id) {
      bOrs ?
        setErrMsg("")
        :
        setErrMsg("")
      isPass = false
    }

    if (isPass) { // 
      setErrMsg("")
      if (bOrs) { // 
        console.log(sForm);
        createSnapshot(sForm).then(res => {
          if (res.data.status === 200) {
            showDialog("", "success")
            setIsSmodalVisible(false)
            getSnapshot()
          }
        })
      } else {  // 
        console.log("");
        createBackup(sForm).then(res => {
          if (res.data.status === 200) {
            showDialog("", "success")
            setIsSmodalVisible(false)
            getBackup()
          }
        })
      }

    }


  }

  //  /modal  type: "s"   type:"b" 
  const showSmodal = (type) => {
    // 
    setErrMsg("")
    // 
    setSForm({
      id,
      name: "",
      disk_id: 0
    })
    if (type === "s") {
      setBOrs(true)
    } else {
      setBOrs(false)
    }
    setIsSmodalVisible(true)
  }

  return (
    <div className='cloud-card backup-snapshot'>
      {
        isSnapshot ?
          // 
          <div className="snapshot-yes">
            <div className="snapshot-yes-top">
              <div className="left">
                <span className='title'>{t('cloud.bas_title1')}</span>
                <span className='text'>{`${total}/5`}</span>
              </div>
              <div className="right">
                {/* <Button className='btn-updown'>{t('cloud.bas_btn1')}</Button> */}
                <Button className='btn-create' onClick={() => showSmodal("s")}>{t('cloud.bas_btn2')}</Button>
              </div>
            </div>
            <div className="main_table">
              <Table columns={columns}
                dataSource={datasource}
                pagination={false}
                loading={isTableLoading}
                rowKey={record => record.id}
              // scroll={datasource.length !== 0 ? {
              //   y: 120,
              //   x: '100%',
              //   scrollToFirstRowOnChange: true
              // } : null}
              />
            </div>
            {/*  */}
            <ComPagination total={total} page={params} changePage={changePage}></ComPagination>
          </div>
          :
          // 
          <div className="snapshot-no">
            <div className="snapshot-no-title">{t('cloud.bas_title1')}</div>
            <div className="snapshot-no-main">
              <img className='snapshot-no-img' src={require('@/assets/images/cloud/snapshot.png')} alt="" />
              {/* <span className='snapshot-no-text'>{t('cloud.bas_columns_tip1')}</span>
              <span className='snapshot-no-money'>$5.00/</span> */}
              <Button className='snapshot-no-btn' onClick={() => showSmodal("s")}>{t('cloud.bas_btn4')}</Button>
            </div>
          </div>
      }
      {/*  */}
      {
        isBackup ?
          // 
          <div className="backup-yes">
            <div className="backup-yes-top">
              <div className="left">
                <span className='title'>{t('cloud.bas_title2')}</span>
                {/* <span className='text'>02,$4.00/</span> */}
              </div>
              <div className="right">
                {/*  */}
                {/* <Button className='btn-close'>{t('cloud.bas_btn3')}</Button> */}
              </div>
            </div>
            <div className="main_table">
              <Table columns={backColumns}
                dataSource={backDatasource}
                pagination={false}
                loading={isBackTableLoading}
                rowKey={record => record.id}
              // scroll={backDatasource.length !== 0 ? {
              //   y: 120,
              //   x: '100%',
              //   scrollToFirstRowOnChange: true
              // } : null}
              />
            </div>
            {/*  */}
            <ComPagination total={backTotal} page={backParams} changePage={changeBackPage}></ComPagination>
          </div>
          :
          // 
          <div className="backup-no">
            <div className="backup-no-title">{t('cloud.bas_title2')}</div>
            <div className="backup-no-main">
              <img className='backup-no-img' src={require('@/assets/images/cloud/backup.png')} alt="" />
              {/* <span className='backup-no-text'>{t('cloud.bas_columns_tip1')}</span>
              <span className='backup-no-money'>$5.00/</span> */}
              <Button className='backup-no-btn' onClick={() => showSmodal("b")}>{t('cloud.bas_btn4')}</Button>
            </div>
          </div>
      }

      {/* / modal */}
      <Modal
        visible={isSmodalVisible}
        footer={null}
        centered={true}
        closable={false}
        style={{ borderRadius: "10px", overflow: "auto" }}
        getContainer={false}
        onCancel={() => setIsSmodalVisible(false)}
      >
        <div className='create-snapshot'>
          <div className="top">
            <div className="title">
              {
                bOrs ?
                  ""
                  :
                  ""
              }
            </div>
          </div>
          <div className="main">
            {
              errMsg ?
                <div className="err">
                  <Alert description={errMsg} type="error" showIcon />
                </div>
                :
                <div className="remind-text">
                  <Alert message={`${bOrs?'':''}`} type="warning" showIcon />
                </div>
            }
            <div className="form">
              <div className="row">
                <Row>
                  <Col span={4}>
                    <span className='label'></span>
                  </Col>
                  <Col span={20}>
                    <Select loading={isSelectLoading} size='large' onDropdownVisibleChange={(open) => showDisk(open)} onChange={getDiskIdValue} >
                      {
                        diskList.map(item => {
                          return <Option key={item.id} value={item.id}>{`${item.name}-${item.size}G`}</Option>
                        })
                      }
                    </Select>
                  </Col>
                </Row>
              </div>
              <div className="row">
                <Row>
                  <Col span={4}>
                    <span className='label'></span>
                  </Col>
                  <Col span={20}>
                    <Input size='large' placeholder={bOrs ? "" : ""} onChange={getNameValue} />
                  </Col>
                </Row>
              </div>
            </div>
          </div>
          <div className="footer">
            <Button className='sub-btn' onClick={doCreateSnapshot}></Button>
            <Button className='cancel-btn' onClick={() => setIsSmodalVisible(false)}></Button>
          </div>
        </div>
      </Modal>
    </div>
  )
}
