import React, { useState, useEffect, useReducer, useRef } from "react";
import { useTranslation } from "react-i18next";
import { useNavigate } from "react-router-dom";
import { Col, Row, Tabs, Select, Form, message, Button, Checkbox, Table, Modal, Popover } from "antd";
import { ExclamationCircleOutlined } from "@ant-design/icons";
import "./network.less";
import * as cloud from "@/api/cloud";
import * as network from "@/api/network";
import * as elastic from '@/api/elastic_ip'
import { showDialog } from '@/utils/util'
import DelDialog from '@/components/delDialog/DelDialog';
import ComPagination from '@/components/comPagination/ComPagination.jsx'
import useMountedRef from '@/utils/UseMountedRef.jsx'
import moment from "moment";
import {
  EllipsisOutlined
} from '@ant-design/icons';
import * as echarts from "echarts";
export default function Network (props) {
  const { t } = useTranslation();
  const [pasForm] = Form.useForm()
  const mountedRef = useMountedRef();
  const navigate = useNavigate()
  const { Option } = Select;
  const columns = [
    {
      title: 'IP' + t('common.address'),
      dataIndex: "ip",
      key: "id"
    },
    {
      title: t("common.gateway"),
      dataIndex: "gateway",
      key: "gateway",
    },
    {
      title: t("common.mask"),
      dataIndex: "subnet_mask",
      key: "subnet_mask",
    },
    {
      title: t("cloud.manage"),
      dataIndex: 'manage',
      key: 'manage',
      width: 100,
      render: (_, record) => (
        <>
          {
            <div className="com-opt-pop" id={'com-opt' + record.id}>
              <Popover getPopupContainer={() => document.getElementById('com-opt' + record.id)}
                content={
                  <>
                    <p className="cloud-opt" onClick={() => editItem(record)}>{record.cloud_name ? t("common.unmount") : t("common.mount")}</p>
                  </>
                } placement="topRight">
                <p className="opt-icon"><EllipsisOutlined /></p>
              </Popover>
            </div>
          }
        </>
      )
    }
  ];
  const vpcColumns = [
    {
      title: 'IP' + t('common.address'),
      dataIndex: "ip",
      key: "id"
    },
    {
      title: t("common.gateway"),
      dataIndex: "gateway",
      key: "gateway",
    },
    {
      title: t("common.mask"),
      dataIndex: "mask",
      key: "mask",
      width: 200
    }];
  /*  */
  const [info, setInfo] = useState({})
  const [optType, setOptType] = useState('vpc')
  const [itemModal, setItemModal] = useState(false)
  const [pasVisible, setPasVisible] = useState(false)
  const [optTitle, setOptTitle] = useState('')
  const [isModalVisible, setIsModalVisible] = useState(false)
  const [title, setTitle] = useState(t('cloud.bas_btn2') + t('cloud.security_group'))
  // ip
  const [ipLoading, setIpLoading] = useState(false)
  const [ipTotal, setIpTotal] = useState(0)
  const [ipList, setIpList] = useState([])
  // vpc
  const [vpc, setVpc] = useState([]);
  const [vpcList, setVpcList] = useState([]);
  // 
  const [group, setGroup] = useState([])
  // 
  const [logList, setLogList] = useState([]);
  // 
  const [flowList, setflowList] = useState({
    total: "",
    used: "",
    leave: "",
    reset_flow_date: "",
  });
  // 
  const [securityList, setsecurityList] = useState([]);
  const id = props.id;
  const [total, setTotal] = useState(0);
  const [isshow, setisshow] = useState(false);

  const [params, setParams] = useState({
    id,
    keywords: "",
    page: 1,
    limit: 20,
    orderby: "id",
    sort: "desc",
  });
  /* ip */
  const [ipParams, setIpParams] = useState({
    id,
    keywords: "",
    page: 1,
    limit: 5,
    orderby: "id",
    sort: "desc",
    data_center_id: props.detail.data_center.id
  });
  const [nettype, setnettype] = useState(1);
  /*  vpc*/
  const getvpcList = async () => {
    try {
      const res = await cloud.vpcdata({ id });
      const temp = [res.data.data]
      setVpc(temp)
    } catch (error) {
      message.error(error.data.msg);
    }
  };
  // vpc
  const getVpc = async () => {
    try {
      const res = await network.getVpc()
      if (res.data.status === 200) {
        const temp = res.data.data.list.filter(item => item.id === props.detail.data_center.id)
        const _vpc = temp[0].vpc.filter(item => item.name !== vpc[0].vpc_name)
        setVpcList(_vpc)
      }
    } catch (error) {

    }
  }
  // 
  const getGroup = async () => {
    try {
      const res = await network.getGroup({
        page: 1,
        limit: 1000
      })
      if (res.data.status === 200) {
        setGroup(res.data.data.list)
      }
    } catch (error) {

    }
  }
  // ip
  const getIpList = async () => {
    try {
      setIpLoading(true)
      const res = await elastic.getCloudIp(ipParams)
      if (mountedRef.current) {
        const temp = res.data.data.list.filter(item=> item.status === 'Active')
        setIpList(temp)
        setIpTotal(temp.length)
        setIpLoading(false)
      }
    } catch (error) {
      showDialog(error.data.msg, 'error')
      setIpLoading(false)
    }
  }
  const closeDialog = () => {
    setIsModalVisible(false)
  }
  const optCancel = () => {
    setPasVisible(false)
  }
  // |
  const handlerPas = (type) => {
    setPasVisible(true)
    setOptType(type)
    pasForm.resetFields()
    if (type === 'vpc') {
      setOptTitle(t("network.change_net"))
      getVpc()
    } else if (type === 'safe') {
      setOptTitle(t("network.add_to_group"))
      getGroup()
    }
  }
  const onFinishFailed = () => {
  }
  // 
  const jumpSafe = () => {
    navigate(`/group_rules?id=${props.detail.security_group.id}`)
  }
  // 
  const surePassword = async (values) => {
    let params = {};
    if (optType === 'vpc') {
      params = {
        id,
        vpc_id: values.type
      }
    } else if (optType === 'safe') {
      params = {
        id: values.type,
        host_id: id
      }
    }
    try {
      if (optType === 'vpc') { // vpc
        showDialog(t('network.tip7'), 'success')
        setPasVisible(false)
        const res = await cloud.changeVpc(params)
        if (res.data.status === 200) {
          showDialog(res.data.msg, 'success')
          getvpcList()
        }
      } else if (optType === 'safe') { // 
        const res = await network.concatCloud(params)
        if (res.data.status === 200) {
          showDialog(t('common.add') + t('common.success'), 'success')
          setPasVisible(false)
          props.refreshDetail()
        }
      }
    } catch (error) {
      showDialog(error.data.msg, 'error')
    }
  }
  //  
  const getflowList = async () => {
    try {
      const res = await cloud.flowdata({ id });
      if (res.data.status === 200) {
        setflowList(res.data.data);
      }
    } catch (error) {
      message.error(error.data.msg);
    }
  };
  //
  const changetype4 = (e) => {
    setnettype(e);
  };
  // 
  const changePage = (page, size) => {
    setIpParams((ipParams) => {
      ipParams.limit = size;
      ipParams.page = page;
      return ipParams;
    });
    getIpList();
  };
  //
  const getstarttime = (type) => {
    console.log(parseInt(new Date().getTime() / 1000), "time");
    let nowtime = parseInt(new Date().getTime() / 1000);
    if (type === 1) {
      return nowtime - 24 * 60 * 60;
    } else if (type === 2) {
      return nowtime - 24 * 60 * 60 * 3;
    } else if (type === 3) {
      return nowtime - 24 * 60 * 60 * 7;
    } else if (type === 4) {
      return nowtime - 24 * 60 * 60 * 30;
    } else if (type === 5) {
      return nowtime - 24 * 60 * 60 * 30 * 6;
    }
  };
  //
  const getList = async (type, timetype) => {
    let start_time = getstarttime(timetype);
    console.log(start_time, "getstarttime");
    let parmar = {
      id,
      type,
      start_time,
    };
    try {
      const res = await cloud.chartdata(parmar);
      const list = res.data.data.list;
      let xAxis = [];
      let yAxis = [];
      let yAxis2 = [];
      list.map((item) => {
        if (type === "bw") {
          xAxis.push(moment(item.time * 1000).format("YYYY-MM-DD HH:mm"));
          yAxis.push(item.in_bw.toFixed(2));
          yAxis2.push(item.out_bw.toFixed(2));
          getbwchart(xAxis, yAxis, yAxis2);
        }
      });

      console.log(xAxis, yAxis, "data");
    } catch (error) {
      // message.error(error.data.msg);
    }
  };
  //
  const getbwchart = (xAxis, yAxis, yAxis2) => {
    var chartDom = document.getElementById("bwchart");
    var myChart = echarts.init(chartDom);
    // console.log(myChart, "myChart1");
    var option;
    option = {
      tooltip: {
        show: true,
        trigger: "axis",
      },
      xAxis: {
        type: "category",
        boundaryGap: false,
        data: xAxis,
      },
      yAxis: {
        type: "value",
      },
      series: [
        {
          name: "(bps)",
          data: yAxis,
          type: "line",
          areaStyle: {},
        },
        {
          name: "(bps)",
          data: yAxis2,
          type: "line",
          areaStyle: {},
        },
      ],
    };
    option && myChart.setOption(option);
  };
  const setshowdialog = () => {
    navigate(`/create_elastic_ip`)
  };
  //
  const handleOk = () => { };
  //
  const handleCancel = () => {
    setisshow(false);
  };
  //
  const handleChange = (e) => { };
  useEffect(() => {
    getvpcList();
    getIpList();
    getflowList();
  }, []);

  useEffect(() => {
    getList("bw", nettype);
  }, [nettype]);

  /*  */
  /* ip */
  /*  / */
  const editItem = (row) => {
    setInfo(row)
    if (!row.cloud_name) { // 
      setItemModal(true)
      // form.setFieldsValue({
      //   id: row.id,
      //   host_id: ''
      // })
    } else { // 
      setInfo(row)
      setTitle(t('common.unmount') + t('network.elastic_ip'))
      setIsModalVisible(true)
    }
  }
  // 
  const submitDel = async () => {
    try {
      const res = await elastic.unmountCloud(info.id)
      if (res.data.status === 200) {
        showDialog(t('common.unmount') + t('common.success'), 'success')
        getIpList()
      }
    } catch (error) {
      showDialog(error.data.msg, 'error')
    }
  }
  /* vpc */

  /*  */

  return (
    <div className="com-content cloud-network">
      <div className="com-card">
        {/* ip */}
        <div className="box">
          <div className="com-tit iptit">
            <h1 className="tit">IP</h1>
            <Button
              className="sub-button"
              type="primary"
              htmlType="submit"
              onClick={setshowdialog}
            >
              IP
            </Button>
          </div>
          <Table
            columns={columns}
            dataSource={ipList}
            rowKey={(record) => record.id}
            pagination={false}
            loading={ipLoading}
            className="cloud-tab"
          />
          {
            ipTotal > 0 && <ComPagination total={ipTotal} page={ipParams} changePage={changePage}
              pageSizeOptions={[5, 10, 20]} ></ComPagination>
          }

        </div>
        {/* vpc */}
        <div className="box">
          <div className="com-tit">
            <h1 className="tit">{t('cloud.vpc_network')}</h1>
            <div className="subtit">
              {t('cloud.vpc_network') + t('cloud.bas_columns_title1')}{vpc[0]?.vpc_name ? vpc[0]?.vpc_name : t('common.none')}
              <span className="changenet" onClick={() => handlerPas('vpc')}>{t('network.change_net')}</span>
            </div>
          </div>
          <Table
            columns={vpcColumns}
            dataSource={vpc}
            pagination={false}
            rowKey={(record) => record.vpc_id || 1}
            className="cloud-tab"
          />
        </div>
        {/*  */}
        <div className="box">
          <div className="com-tit">
            <h1 className="tit">{t('cloud.security_group')}</h1>
          </div>
          <div className="security">
            {props.detail.security_group.id === 0 ? (
              <div className="security_box">
                <div>
                  <img src={require('@/assets/images/cloud/warning.png')} alt="" className="war-icon" />
                </div>
                <div className="nodata">
                  {t('network.tip5')}
                  <span className="changenet" onClick={() => handlerPas('safe')}> {t('network.add_to_group')}</span>
                </div>
              </div>
            ) : (
              <div className="security_box">
                <div className="source">
                  {t('network.tip6')}<span className="filename">{props.detail.security_group.name}</span>
                </div>
                <Button className="sec_btn" onClick={jumpSafe}>{t('common.manage')}</Button>
              </div>
            )}
          </div>
        </div>
        <div className="box">
          <div className="com-tit">
            <h1 className="tit"></h1>
          </div>
          <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 30 }}>
            <Col xs={{ span: 20 }} lg={{ span: 10 }}>
              <div className="cloud-card leftnet">
                <div className="net_item">
                  <span className="net_itemtit"></span>
                  <span>{flowList.total}</span>
                </div>
                <div className="net_item">
                  <span className="net_itemtit"></span>
                  <span>{flowList.used}</span>
                </div>
                <div className="net_item">
                  <span className="net_itemtit"></span>
                  <span>{flowList.leave}</span>
                </div>
                <div className="net_item">
                  <span className="net_itemtit"> </span>
                  <span>{flowList.reset_flow_date}</span>
                </div>
              </div>
            </Col>
             <Col xs={{ span: 24 }} lg={{ span: 12 }}>
              <div className="cloud-card chartbox">
                <p className="chart_title"></p>
                <div className="chart_type">
                  <div
                    className={`chart_type_item ${nettype === 1 ? "active" : ""
                      }`}
                    onClick={() => changetype4(1)}
                  >
                    24
                  </div>
                  <div
                    className={`chart_type_item ${nettype === 2 ? "active" : ""
                      }`}
                    onClick={() => changetype4(2)}
                  >
                    3
                  </div>
                  <div
                    className={`chart_type_item ${nettype === 3 ? "active" : ""
                      }`}
                    onClick={() => changetype4(3)}
                  >
                    7
                  </div>
                  <div
                    className={`chart_type_item ${nettype === 4 ? "active" : ""
                      }`}
                    onClick={() => changetype4(4)}
                  >
                    1
                  </div>
                  <div
                    className={`chart_type_item ${nettype === 5 ? "active" : ""
                      }`}
                    onClick={() => changetype4(5)}
                  >
                    
                  </div>
                </div>
                <div id="bwchart" className="echarts"></div>
              </div>
            </Col>
          </Row>
        </div>
        {/*  <Modal
          title="IP"
          maskClosable="true"
          visible={isshow}
          cancelText=""
          okText=""
          closeIcon
          onOk={handleOk}
          onCancel={handleCancel}
          maxwidth="100%"
          className="modal"
        >
          <div className="subtit">
            IPIP
          </div>
          <div className="modal_con">
            <div>IP</div>
            <Select
              defaultValue="lucy"
              style={{
                width: "50%",
              }}
              onChange={handleChange}
            >
              <Option value="jack">Jack</Option>
              <Option value="lucy">Lucy</Option>
            </Select>
            <Button className="sec_btn">IP</Button>
          </div>
        </Modal> */}
      </div>
      {/*  */}
      {
        isModalVisible && <DelDialog
          closeDialog={closeDialog} submitDel={submitDel}
          title={title}
          text={
            t('elastic.tip1') + t('common.unmount') + t('network.elastic_ip') + info.ip + '\n' + t('elastic.tip2')
          }
        ></DelDialog>
      }
      {/* | */}
      <Modal title={optTitle} forceRender
        visible={pasVisible} onCancel={optCancel}
        footer={null} className="power-modal password-modal" closable={false}>
        <Form form={pasForm}
          labelAlign='left'
          initialValues={{

          }}
          onFinish={surePassword}
          onFinishFailed={onFinishFailed}
          autoComplete="off"
        >
          {
            optType === 'vpc' ? (
              <Form.Item label={t("common.new") + 'VPC'} colon={false} name="type"
              >
                <Select
                  getPopupContainer={triggerNode => triggerNode.parentElement}
                >
                  {
                    vpcList.map(item => {
                      return <Option value={item.id} key={item.id}>{item.name}</Option>
                    })
                  }
                </Select>
              </Form.Item>
            ) : (
              <Form.Item label={t("network.choose_group")} colon={false} name="type"
              >
                <Select
                  getPopupContainer={triggerNode => triggerNode.parentElement}
                >
                  {
                    group.map(item => {
                      return <Option value={item.id} key={item.id}>{item.name}</Option>
                    })
                  }
                </Select>
              </Form.Item>
            )
          }
          {/*  */}
          {
            props.type !== 'off' && optType === 'vpc' &&
            (
              <>
                <div className="com-danger">
                  <p className="tit">{t("cloud.tip23")}</p>
                  <div className="des">
                    <p>{t("cloud.tip24")}</p>
                    <p>{t("cloud.tip25")}</p>
                  </div>
                </div>
                <Form.Item colon={false}
                  required name="force"
                  label={
                    t("cloud.force_shutdown")
                  }
                  valuePropName="checked" rules={[
                    {
                      validator: (_, value) =>
                        value ? Promise.resolve() : Promise.reject(new Error(t("cloud.tip26"))),
                    },
                  ]} className="force-off">
                  <Checkbox>{t("cloud.agree_force_shutdown")}</Checkbox>
                </Form.Item>
              </>
            )
          }
          <Form.Item className="opt-btn">
            <Button type="primary" htmlType="submit" className="sure">
              {t("common.sure")}
            </Button>
            <Button onClick={optCancel} className="cancel">
              {t("common.cancel")}
            </Button>
          </Form.Item>
        </Form>
      </Modal>
    </div>
  );
}
