import React, { useState, useEffect, useRef } from 'react';
import { Col, Row, Button, Modal, Select, Input, Checkbox } from 'antd';
import Swal from "sweetalert2";
import moment from 'moment'
import { useTranslation } from "react-i18next";
import * as cloud from '@/api/cloud';
import { getCommon } from '@/api/common'
import '../../cloud/cloud.css';
import './Summary.less';
import { useNavigate } from "react-router-dom";
import { showDialog } from '../../../../utils/util';
import { orderDetails, account, gateway, pay, creditPay, getPayStatus, onlinePay, promoCode } from "@/api/finance"
let timer = null;
let time = 300000;  // 5
export default function Summary() {
  const { Option } = Select
  const { TextArea } = Input;
  const { t } = useTranslation()
  const navigate = useNavigate();
  // id
  const [id, setId] = useState(
    () => window.location.hash.split("=")[1]
  )
  // 
  const [CodeInput, setCodeInput] = useState()

  const [refundStste] = useState(
    {
      Pending: "",
      Suspending: "",
      Suspend: "",
      Suspended: "",
      Refund: "",
      Reject: "",
      Cancelled: ""
    }
  )

  // 
  const [promoTotal, setPromoTotal] = useState(0)

  // 
  const [isAdd, setIsAdd] = useState(true)

  // 
  const [refundMsg, setRefundMsg] = useState()

  // id
  const [activeId, setActiveId] = useState(1)
  // 
  const [isShow, setIsShow] = useState(false)

  // 
  const [cloundDetail, setCloundDetail] = useState()
  // 
  const [hostDetail, setHostDetail] = useState()
  // 
  const endDayCount = hostDetail ? Math.floor((hostDetail.due_time * 1000 - Date.now()) / (1000 * 60 * 60 * 24)) : null
  // 
  const [hostPromoCode, setHostPromoCode] = useState()
  // 
  const [common, setCommon] = useState()
  // 
  const [status] = useState({
    Unpaid: { text: "" },
    Pending: { text: "" },
    Active: { text: "" },
    Suspended: { text: "" },
    Deleted: { text: "" },
    Failed: { text: "" },
  })
  // 
  const [refundData, setRefundData] = useState()
  // 
  const [refundParams, setParams] = useState({
    host_id: Number(id),
    suspend_reason: [],
    type: ""
  })


  // 
  const [visible, setVisible] = useState(false)
  // id
  const [orderId, setOrderId] = useState("");
  // 
  const [money, setMoney] = useState()
  // 
  const [curPay, setCurPay] = useState("");
  // 
  const [isShowPay, setIsShowPay] = useState(true);
  // html
  const [payHtml, setPayHtml] = useState("");
  //  loading
  const [loading, setLoading] = useState(false);
  // 
  const [balanceChecked, setBalanceChecked] = useState(false);
  // 
  const [payList, setPayList] = useState([]);
  // 
  const [balance, setBalance] = useState()

  // 
  const [isEditNote, setIsEditNote] = useState(true)

  // 
  const [notes, setNotes] = useState()

  // 
  const editNotes = () => {
    if (isEditNote) {
      setNotes(hostDetail.notes)
      setIsEditNote(!isEditNote)
    } else {
      console.log("notes", notes);
      const params = {
        id,
        notes
      }
      // 
      cloud.changeNotes(params).then(res => {
        if (res.data.status === 200) {
          // 
          getHost()
          setIsEditNote(!isEditNote)
        }

      })


    }

  }

  const notesInputChange = (e) => {
    setNotes(e.target.value)
  }


  // 
  const getAccount = () => {
    account().then(res => {
      if (res.data.status === 200) {
        const credit = res.data.data.account.credit
        setBalance(credit)
      }
    })
  }

  // 
  const showPayModal = (id, amount) => {
    // 
    if (timer) {  // 
      clearInterval(timer)
    }
    setBalanceChecked(false)
    //  5
    time = 300000;

    // 
    setMoney(amount)
    // id
    setOrderId(id)

    //  modal
    setVisible(true)
    // 
    changePayWay(payList[0].name, id);
    // 
    pollingStatus(id)
  }

  // 
  const getPayWay = () => {
    gateway().then(res => {
      setPayList(res.data.data.list);
      setCurPay(res.data.data.list[0].name);
    })
  }

  // 
  const changePayWay = (e, id) => {
    setCurPay(e);
    //    &&  
    if (
      Number(balance) >= Number(money) &&
      balanceChecked
    ) {
      return false;
    }
    const params = {
      gateway: e,
      id: (typeof id !== "object" && id) || orderId,
    };
    // 
    pay(params).then(res => {
      setPayHtml(res.data.data.html);
    })
  }

  // 
  const useBalance = (e) => {
    setBalanceChecked(e.target.checked);
    creditPay({
      id: orderId,
      use: e.target.checked ? 1 : 0,
    }).then(res => {
      // id
      const tempId = res.data.data.id;
      // 
      orderDetails(tempId).then(result => {
        const orderRes = result.data.data.order;
        // 
        if (e.target.checked) { // 
          if (Number(balance) >= Number(orderRes.amount)) {
            //   
            setIsShowPay(false);
          } else {
            //   
            setIsShowPay(true);
            changePayWay(curPay, tempId);
          }
        } else { // 
          setIsShowPay(true);
          changePayWay(curPay, tempId);
        }
      })
    })
  }

  // 
  const pollingStatus = (id) => {
    if (timer) {
      clearInterval(timer)
    }
    timer = setInterval(async () => {
      const res = await getPayStatus(id ? id : orderId);
      time = time - 2000
      if (res.data.code === "Paid") { // 
        showDialog(res.data.msg, "success");
        clearInterval(timer);
        setVisible(false);
        getHost()
        return false;
      }
      // 5
      if (time === 0) {
        clearInterval(timer);
        setVisible(false);
        return false;
      }
    }, 2000)

  };


  // 
  const handleOk = async () => {
    try {
      const params = {
        gateway: curPay,
        id: orderId,
      };
      await onlinePay(params);
    } catch (error) { }
  };

  // 
  const handleCancel = () => {
    // 
    setVisible(false);
    clearInterval(timer);

    if (balanceChecked) { // 
      // 
      const params = {
        id: orderId,
        use: 0
      }
      creditPay(params).then(res => { }).catch(error => { })
    }
    navigate("/finance/order", { replace: true });
  };


  //  
  const [renewData, setRenewData] = useState()
  const [isRenewModal, setIsRenewModal] = useState(false)
  const inputVal = useRef(null)
  const [showId, setShowId] = useState([1, 2, 3])
  const [codeData, setCodeData] = useState([])
  //  modal
  const showRenewModal = () => {
    // 
    cloud.renew({ id }).then(res => {
      if (res.data.status === 200) {
        // 
        setPromoTotal(0)
        // 
        setCodeData([])
        // 
        setCodeInput(null)
        setIsRenewModal(true)
        const host = res.data.data.host
        let i = 1
        host.map(item => {
          item.id = i
          i++
        })
        setRenewData(host)
      }
    })
  }

  // 
  const getCode = () => {
    const promo_code = inputVal.current.input.value
    if (!promo_code) {
      showDialog("", "error")
      return false
    }
    // 
    let isSame = false
    codeData.map(item => {
      if (item.promo_code === promo_code) {
        isSame = true
        console.log("");
        return false
      }
    })
    if (isSame) {
      showDialog("", "warning")
      return false
    }
    const amount = renewData[activeId - 1].price
    const billing_cycle_time = renewData[activeId - 1].duration
    const params = {
      promo_code,
      scene: "Renew",
      total: amount,
      cycles: [{
        product_id: id,
        amount,
        billing_cycle_time,
        congig_options: {
          // 
          package_id: cloundDetail.package.id
        }
      }]
    }

    cloud.usePromoCode(params).then(res => {
      if (res.data.status === 200) {
        let list = res.data.data
        if (codeData && codeData.length > 0) { // 
          // 
          if (!codeData[0].overlay) {
            showDialog("", "warning")
          } else {
            if (list.overlay) { //  
              list.promo_code = promo_code
              codeData.push(list)
              setPromoTotal(promoTotal => {
                return Number(promoTotal) + Number(list.discount)
              })
              setCodeData(codeData)
              // 
              setIsShow(false)
            } else {
              showDialog("", "warning")
            }
          }

        } else {
          //  
          list.promo_code = promo_code
          codeData.push(list)
          setPromoTotal(promoTotal => {
            return Number(promoTotal) + Number(list.discount)
          })
          setCodeData(codeData)
          // 
          setIsShow(false)
          // inputVal.current.input.value = null
        }
      }
    })
  }

  //   
  const delAllCode = () => {
    setCodeData([])
    setPromoTotal(0)
  }

  // 
  const delCode = (promo_code) => {

    let arr1 = codeData.filter(item => item.promo_code === promo_code)
    // 
    setPromoTotal(promoTotal => {
      return promoTotal - Number(arr1[0].discount)
    })

    let arr = codeData.filter(item => item.promo_code !== promo_code)

    console.log(codeData);
    setCodeData(arr)
  }

  // /modal
  const [isShowModal, setIsShowModal] = useState(false)

  // 
  const [errMsg, setErrMsg] = useState("")
  // 
  const [errTime, setErrTime] = useState("")
  // 
  const [infoTime, setInfoTime] = useState("")
  const [infoMsg, setInfoMsg] = useState("")
  const [infoEndTime, setInfoEndTime] = useState("")

  // 
  const getReason = (e) => {
    setParams({ ...refundParams, suspend_reason: e })
  }

  const getSelfReason = (e) => {
    setParams({ ...refundParams, suspend_reason: e.target.value })
  }

  //  
  const getType = (e) => {
    setParams({ ...refundParams, type: e })
  }

  //  /modal
  const showModal = () => {
    // 
    setParams({
      host_id: Number(id),
      suspend_reason: null,
      type: ""
    })

    // 
    cloud.refund({ host_id: id }).then(res => {
      if (res.data.status === 200) {
        let list = res.data.data
        setRefundData(list)
        if (list.allow_refund == 1) {  // 
          // modal
          setIsShowModal(true)
        } else {
          // setIsShowModal(true)
          // let time = new Date().getTime()
          // time = moment(time).format("YYYY.MM.DD HH:mm")
          // setErrMsg(time)
          showDialog("", "warning")
        }
      }
    })

  }

  // s
  const doRefund = () => {
    console.log(refundParams);
    let isPass = true
    if (!refundParams.suspend_reason) {
      showDialog("", "error")
      isPass = false
    }
    if (!refundParams.type) {
      showDialog("", "error")
      isPass = false
    }
    if (isPass) {
      cloud.postRefund(refundParams).then(res => {
        if (res.data.status === 200) {
          showDialog("", "success")
          setIsShowModal(false)
          // 
          getHost()
          // 
          getRefundMsg()
        }
      })
    }

  }

  // 
  const quitStop = () => {
    Swal.fire({
      text: ``,
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#5C5EE4',
      confirmButtonText: '',
      cancelButtonText: '',
      customClass: {
        cancelButton: 'ssh-cancel-button',
      }
    }).then((result) => {
      if (result.value) {
        // 
        console.log(refundMsg);
        const id = refundMsg.id
        cloud.quitRefund({ id }).then(res => {
          if (res.data.status === 200) {
            showDialog("!", "success")
            // 
            getRefundMsg()
          }
        })
      }
    })
  }

  // 
  const getCloudDetails = () => {
    cloud.getCloudDetail(id).then(res => {
      if (res.data.status === 200) {
        setCloundDetail(res.data.data)
      }
    })
  }

  // 
  const getRefundMsg = () => {
    cloud.refundMsg(id).then(res => {
      if (res.data.status === 200) {
        const refund = res.data.data.refund
        setRefundMsg(refund)
        if (refund && refund.status === "Reject") { // 
          let time = refund.create_time * 1000
          setErrTime(moment(time).format("YYYY.MM.DD HH:mm"))
          if (refund.type) {
            if (refund.type === "Immediate") {
              setErrMsg("")
            } else {
              setErrMsg("")
            }
          }
        }
        if (refund && refund.status === "Suspended") {
          let time = refund.create_time * 1000
          setInfoTime(moment(time).format("YYYY.MM.DD HH:mm"))
          if (refund.type) {
            if (refund.type === "Immediate") {
              // setErrMsg("")
              setInfoMsg("")
            } else {
              setInfoMsg("")
            }
          }
        }
      }
    })
  }

  // 
  const getHost = () => {
    cloud.host(id).then(res => {
      if (res.data.status === 200) {
        let list = res.data.data.host
        setHostDetail(list)
      }
    })
  }

  // 
  const getUserCommon = () => {
    getCommon().then(res => {
      if (res.data.status === 200) {
        setCommon(res.data.data)
      }
    })
  }
  // 
  const doSub = () => {
    let num = activeId
    if (num > 1) {
      num -= 1
      setActiveId(num)
      delAllCode()
    }
    if (showId[0] > 1) {
      let newIds = showId
      newIds[0] = newIds[0] - 1
      newIds[1] = newIds[1] - 1
      newIds[2] = newIds[2] - 1
      setShowId(newIds)
    }
  }

  // 
  const doAdd = () => {
    let num = activeId
    if (num < renewData.length) {
      num += 1
      setActiveId(num++)
      delAllCode()
    }
    if (showId[2] < renewData.length) {
      let newIds = showId
      newIds[0] = newIds[0] + 1
      newIds[1] = newIds[1] + 1
      newIds[2] = newIds[2] + 1
      setShowId(newIds)
    }
  }

  // 
  const doRenew = () => {
    let customfield = []
    if (codeData) {
      codeData.map(item => {
        customfield.push(item.promo_code)
      })
    }
    const params = {
      id: Number(id),
      billing_cycle: renewData[activeId - 1].billing_cycle,
      customfield: {
        "promo_code": customfield
      }
    }
    // 
    cloud.doRenw(params).then(res => {
      if (res.data.status === 200) {
        setIsRenewModal(false)
        const orderId = res.data.data.id
        const amount = renewData[activeId - 1].price - promoTotal
        setOrderId(orderId)
        // setVisible(true)
        // 
        showPayModal(orderId, amount)

        // showDialog("", "success")
      }
    })
  }


  const codeInputChange = (e) => {
    // console.log(e.target.value);
    setCodeInput(e.target.value)
  }

  // 
  const getYhCode = () => {
    promoCode(id).then(res => {
      if (res.data.status === 200) {
        const data = res.data.data.promo_code
        // const arr = ["sssss","asgdjkas"]
        setHostPromoCode(data)
      }
    })
  }

  // 
  const showReson = () => {
    showDialog(`${refundMsg.reject_reason}`, "info")
  }

  useEffect(() => {
    getCloudDetails()
    getHost()
    getUserCommon()
    getPayWay()
    getAccount()
    getYhCode()
    getRefundMsg()
    return () => clearInterval(timer);
  }, [])

  return (
    <div className="summary">
      {
        cloundDetail && hostDetail && common ?
          <div>
            <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 30 }}>
              {/*  */}
              <Col xs={{ span: 24 }} lg={{ span: 12 }}>
                <div className="cloud-card">
                  <p className="tit">{t("cloud.product_info")}</p>
                  <div className="item">
                    <p className="l-tit"></p>
                    <div className="r-text">
                      <p>{cloundDetail.package.name ? cloundDetail.package.name : '--'}</p>
                    </div>
                  </div>
                  <div className="item">
                    <p className="l-tit"></p>
                    <div className="r-text">
                      {/* cal */}
                      <p>{cloundDetail.cal.cpu ? cloundDetail.cal.cpu : '--'} vCPU</p>
                      <p>{cloundDetail.cal.memory ? cloundDetail.cal.memory / 1024 : '--'} G RAM</p>
                      <p>{cloundDetail.cal.disk_size ? cloundDetail.cal.disk_size : '--'} G SSD Storage</p>
                      {/* bw */}
                      <p>{cloundDetail.bw.flow ? cloundDetail.bw.flow : '--'} TB/mo Transfer</p>
                      <p>{cloundDetail.bw.bw ? cloundDetail.bw.bw : '--'} Gbps Port speed</p>
                    </div>
                  </div>
                  <div className="item">
                    <p className="l-tit"></p>
                    <div className="r-text">
                      <p>{hostDetail.name ? hostDetail.name : '--'}</p>
                    </div>
                  </div>
                  <div className="item">
                    <p className="l-tit">IP</p>
                    <div className="r-text">
                      <p>{cloundDetail.ip ? cloundDetail.ip : '--'}</p>
                    </div>
                  </div>
                </div>
              </Col>
              {/*  */}
              <Col xs={{ span: 24 }} lg={{ span: 12 }}>
                <div className="cloud-card">
                  <p className="tit">{t("cloud.finance_info")}</p>
                  <div className="item">
                    <p className="l-tit"></p>
                    <div className="r-text">
                      <p>{common.currency_prefix}{hostDetail.first_payment_amount}{common.currency_code}</p>
                    </div>
                  </div>
                  <div className="item">
                    <p className="l-tit"></p>
                    <div className="r-text">
                      <p>{status[hostDetail.status].text}</p>
                    </div>
                  </div>
                  <div className="item">
                    {/*  */}
                    <p className="l-tit">{t("common.begin_time")}</p>
                    <div className="r-text">
                      <p>{hostDetail.active_time ? moment(hostDetail.active_time * 1000).format("YYYY-MM-DD HH:mm") : '--'}</p>
                    </div>
                  </div>
                  <div className="item">
                    {/*  */}
                    <p className="l-tit">{t("common.due_time")}</p>
                    <div className="r-text">
                      <p>{hostDetail.due_time * 1000 ? moment(hostDetail.due_time * 1000).format("YYYY-MM-DD HH:mm") : '--'}{hostDetail.active_time && hostDetail.due_time ? `(${endDayCount})` : null}</p>
                    </div>
                  </div>
                  <div className="item">
                    <p className="l-tit"></p>
                    <div className="r-text">
                      <p>{common.currency_prefix}{hostDetail.renew_amount}{common.currency_code}/{hostDetail.billing_cycle_name ? hostDetail.billing_cycle_name : null}

                        {
                          //      
                          hostDetail.status === 'Unpaid' || hostDetail.status === 'Failed' ? null
                            :
                            (
                              // 
                              refundMsg ?
                                (
                                  //  
                                  //  
                                  refundMsg.status === "Cancelled" || refundMsg.status === "Reject" ?
                                    <>
                                      <span className='renew-btn' onClick={showRenewModal}></span>
                                      <span className='stop-btn' onClick={showModal}></span>
                                    </>
                                    :
                                    //   
                                    // || refundMsg.status === "Suspending" 
                                    refundMsg.status === "Pending" || refundMsg.status === "Suspend" ?
                                      <>
                                        <span className='un-renew-btn'></span>
                                        <span className='stoping-btn'>{refundStste[refundMsg.status]}</span>
                                        <span className='quit-stop-btn' onClick={quitStop}></span>
                                      </>
                                      :
                                      //  
                                      <>
                                        <span className='un-renew-btn'></span>
                                        <span className='stoping-btn'>{refundStste[refundMsg.status]}</span>
                                      </>
                                )
                                :
                                (<>
                                  <span className='renew-btn' onClick={showRenewModal}></span>
                                  <span className='stop-btn' onClick={showModal}></span>
                                </>)

                            )
                        }
                      </p>
                    </div>
                  </div>
                  <div className="item">
                    <p className="l-tit"></p>
                    <div className="r-text">
                      <p>{
                        hostPromoCode && hostPromoCode.length > 0 ?
                          hostPromoCode.map(item => {
                            return <span key={item}>{item}&nbsp;</span>
                          })
                          :
                          '--'
                      }</p>
                    </div>
                  </div>
                  <div className="item">
                    <p className="l-tit"></p>
                    <div className="r-text ">
                      <div className='r-notes'>
                        {
                          isEditNote ?
                            <p>{hostDetail.notes ? hostDetail.notes : '--'}</p>
                            :
                            <Input className='notes-input' onChange={notesInputChange} value={notes} ></Input>
                        }
                        <div className='notes-btn' onClick={editNotes}></div>
                      </div>

                    </div>
                  </div>
                  <div className="item">
                    {
                      errMsg ?
                        <div className="err-msg">
                          <div className='err-text'> ({errTime}<span className='important'>&nbsp;{errMsg}&nbsp;</span><a onClick={showReson}></a> )</div>
                        </div>
                        :
                        null
                    }
                    {
                      infoMsg ?
                        <div className="info-msg">
                          <div className='info-text'> ({infoTime}<span className='important'>&nbsp;{infoMsg}&nbsp;</span>,{infoEndTime})</div>
                        </div>
                        :
                        null
                    }
                  </div>
                </div>
              </Col>
            </Row>
            {/* modal */}
            {
              refundData ?
                <Modal
                  visible={isShowModal}
                  footer={null}
                  centered={true}
                  closable={false}
                  style={{ borderRadius: "10px", overflow: "auto" }}
                  maskClosable={true}
                  keyboard={true}
                  getContainer={false}
                  onCancel={() => setIsShowModal(false)}
                >
                  <div className="modal-unopen">
                    <div className="modal-title">
                      {
                        refundData.host.amount >= 0 ?
                          ""
                          :
                          ""
                      }
                    </div>
                    <div className="modal-form">
                      <Row>
                        <Col span={5}>
                          <div className="row-l">
                            
                          </div>
                        </Col>
                        <Col span={19}>
                          <div className="row-r">
                            <div className="item" style={{ padding: "15px 0" }}>
                              <p className="l-tit"></p>
                              <div className="r-text">
                                {/* cal */}
                                <p>{cloundDetail.cal.cpu} vCPU</p>
                                <p>{cloundDetail.cal.memory / 1024} G RAM</p>
                                <p>{cloundDetail.cal.disk_size} G SSD Storage</p>
                                {/* bw */}
                                <p>{cloundDetail.bw.flow} TB/mo Transfer</p>
                                <p>{cloundDetail.bw.bw} Gbps Port speed</p>
                              </div>
                              <p className='l-tit'></p>
                              <div className="r-text">
                                <p>{moment(hostDetail.active_time * 1000).format("YYYY-MM-DD HH:mm")}</p>
                              </div>
                              {
                                refundData.host.amount >= 0 ?
                                  <div>
                                    <p className='l-tit'></p>
                                    <div className="r-text">
                                      <p>{common.currency_prefix}{refundData.host.first_payment_amount}{common.currency_code}</p>
                                    </div>
                                  </div>
                                  :
                                  null
                              }
                            </div>
                          </div>
                        </Col>
                      </Row>
                      <Row>
                        <Col span={5}>
                          <div className="row-l row-center">
                            
                          </div>
                        </Col>
                        <Col span={19}>
                          <div className="row-r">
                            {
                              refundData.reason_custom !== 1 ?
                                <Select mode="multiple" showArrow allowClear size='large' style={{ width: "100%" }} onChange={getReason}>
                                  {
                                    refundData.reasons.map(item => {
                                      return <Option key={item.id} value={item.id}>{item.content}</Option>
                                    })
                                  }
                                </Select>
                                :
                                <TextArea onChange={getSelfReason} rows={4} placeholder="" />
                            }
                          </div>
                        </Col>
                      </Row>
                      <Row>
                        <Col span={5}>
                          <div className="row-l row-center">
                            
                          </div>
                        </Col>
                        <Col span={19}>
                          <div className="row-r">
                            <Select size='large' style={{ width: "100%" }} onChange={getType}>
                              <Option value="Expire"></Option>
                              <Option value="Immediate"></Option>
                            </Select>
                          </div>
                        </Col>
                      </Row>
                      {
                        refundData.host.amount >= 0 ?
                          <Row>
                            <Col span={5}>
                              <div className="row-l">
                                
                              </div>
                            </Col>
                            <Col span={19}>
                              <div className="row-right">

                                {
                                  refundParams.type === "Immediate" ?
                                    // 
                                    common.currency_prefix + refundData.host.amount + common.currency_code
                                    :
                                    common.currency_prefix + '0' + common.currency_code
                                  // 
                                }
                                {/* {common.currency_prefix}{refundData.host.amount} */}
                              </div>
                            </Col>
                          </Row>
                          :
                          null
                      }
                    </div>
                    <div className="modal-footer">
                      <Button className='btn-ok' onClick={doRefund}>
                        {
                          refundData.host.amount >= 0 ?
                            ""
                            :
                            ""
                        }
                      </Button>
                      <Button className='btn-no' onClick={() => setIsShowModal(false)}></Button>
                    </div>
                  </div>
                </Modal>
                :
                null
            }
            {/* modal */}
            <Modal
              visible={isRenewModal}
              footer={null}
              centered={true}
              closable={false}
              style={{ borderRadius: "10px", overflow: "auto" }}
              maskClosable={true}
              keyboard={true}
              getContainer={false}
              width="708px"
              onCancel={() => setIsRenewModal(false)}
            >
              <div className="modal-renew">
                <div className="modal-title">
                  
                </div>
                <div className="modal-top">
                  <div className="top-l"><img onClick={doSub} src={require('@/assets/images/cloud/left.png')} /></div>
                  <div className="top-m">
                    {
                      renewData ? renewData.map(item => {
                        return (
                          <span onClick={() => { setActiveId(item.id); delAllCode() }} key={item.id} className={item.id === showId[0] || item.id === showId[1] || (item.id === showId[2]) ? (item.id === activeId ? "m-card active" : "m-card") : "m-card hiden"}>
                            <span className="cycle">{item.billing_cycle}</span>
                            <span className="money">
                              <span className='money-prefix'>{common.currency_prefix}</span>
                              <span className="money-num">{item.price}</span>
                            </span>
                            {/* <span className='true-money'>
                              <span className='money-prefix'>{common.currency_prefix}</span>
                              <span className="money-num">{item.price}</span>
                            </span> */}
                          </span>
                        )
                      })
                        :
                        null
                    }
                  </div>
                  <div className="top-r"  ><img onClick={doAdd} src={require('@/assets/images/cloud/right.png')} /></div>
                </div>
                <div className="modal-middle">
                  <div className="main">
                    <div className="text"></div>
                    <div className="money">
                      <span className='pre'>{common.currency_prefix}</span>
                      <span className='num'>{renewData ? renewData[activeId - 1].price - promoTotal > 0 ? renewData[activeId - 1].price - promoTotal : 0 : 0}</span>
                      <a className='btn' onClick={() => { setIsShow(!isShow); setCodeInput(null) }}></a>
                    </div>
                    <div className="operation" style={{ display: isShow ? "" : "none" }}>
                      <Input placeholder='' className='o-input' onChange={codeInputChange} value={CodeInput} ref={inputVal} /><Button className='o-btn' onClick={getCode}></Button>
                    </div>
                    <div className="details">
                      {
                        codeData ?
                          codeData.map(item => {
                            return (
                              <span className='d-item' key={item.promo_code}>
                                <span className='item-pre'>-{common.currency_prefix}{item.discount}</span>
                                <span className="item-code">({item.promo_code})</span>
                                <span className='item-out' onClick={() => { delCode(item.promo_code) }}><div className='item-in'></div></span>
                              </span>
                            )
                          })
                          :
                          null
                      }
                    </div>
                  </div>
                </div>
                <div className="modal-bottom">
                  <Button className='btn-ok' onClick={doRenew}></Button>
                  <Button className='btn-no' onClick={() => setIsRenewModal(false)}></Button>
                </div>
              </div>
            </Modal>
            {/* modal */}
            <Modal
              visible={visible}
              title={t("common.pay")}
              onOk={handleOk}
              onCancel={handleCancel}
              className="pay-modal"
              closable={false}
              getContainer={false}
              footer={
                !isShowPay ? [
                  <Button
                    key="submit"
                    className="sure"
                    loading={loading}
                    onClick={handleOk}
                  >
                    {t("cloud.sure_pay")}
                  </Button>,
                  <Button key="back" onClick={handleCancel} className="cancel">
                    {t("common.cancel")}
                  </Button>,
                ]
                  : null
              }
            >
              <div className="cloud-pay-box">
                <div className="item first">
                  <div className="l-text">{t("common.pay_way")}</div>
                  <div className="r-text-l">
                    {payList.length > 0 && (
                      <Select value={curPay} onChange={changePayWay}>
                        {payList.map((item) => {
                          return (
                            <Option value={item.name} key={item.id}>
                              {item.title}
                            </Option>
                          );
                        })}
                      </Select>
                    )}
                    <Checkbox onChange={useBalance} checked={balanceChecked}>
                      {t("cloud.use_balance")}
                    </Checkbox>
                  </div>
                </div>
                <div className="item">
                  {/*  */}
                  <div className="l-text">{t("cloud.order_amount")}</div>
                  <div className="r-text-l order-num">&nbsp;{money}</div>
                </div>
                <div className="item last">
                  {/*  */}
                  <div className="l-text">{t("cloud.pay_amount")}</div>
                  <div className="r-text-l pay-num">
                    <p className="paid">
                      
                      {
                        //  - 0 
                        balanceChecked
                          ? (Number(money) -
                            Number(balance) <=
                            0
                            ? 0
                            : Number(money) -
                            Number(balance)
                          ).toFixed(2)
                          : Number(money).toFixed(2)
                      }
                    </p>
                    {balanceChecked && (
                      <p className="balance">
                        -
                        {/* >=       */}
                        {(Number(money) >= Number(balance)
                          ? Number(balance)
                          : Number(money)
                        ).toFixed(2)}
                      </p>
                    )}
                  </div>
                </div>
                {
                  // 
                  isShowPay && (
                    <div className="item pay-html">
                      {/*  */}
                      {/* <img src={payHtml} alt="" /> */}
                      {/* <div dangeroussetinnerhtml={{ __html: payHtml }}></div> */}
                      <div dangerouslySetInnerHTML={{ __html: payHtml }}></div>
                    </div>
                  )
                }
              </div>
            </Modal>
          </div >
          :
          null
      }

    </div >
  )
}
