import React, { useState, useEffect } from 'react'
import ComPagination from '@/components/comPagination/ComPagination.jsx'
import { Table, Button, Modal, Row, Col, Input, Select, Alert, Popover } from 'antd';
import { useNavigate } from "react-router-dom";
import {
  EllipsisOutlined
} from '@ant-design/icons';
import moment from 'moment';
import Swal from "sweetalert2";
import './Disk.less'

import { getDisk, umount, delDisk, getDiskById, mount, postRefund, refundMsg, refund } from "@/api/cloud"
import { getCommon } from "@/api/common";
import { showDialog } from '@/utils/util';
import { useTranslation } from "react-i18next";
import DelDialog from '@/components/delDialog/DelDialog';

export default function Transaction() {
  const navigate = useNavigate()
  const { t } = useTranslation()
  const { Option } = Select
  const { TextArea } = Input;
  // 
  const [total, setTotal] = useState(200)
  const [params, setParams] = useState({
    keywords: '',
    page: 1,
    limit: 20,
    orderby: 'id',
    sort: 'desc'
  })

  // /
  const [isModalVisible, setIsModalVisible] = useState(false)
  const [modalText, setModalText] = useState("")
  const [modalTitle, setModalTitle] = useState("")
  const [modalData, setModalData] = useState({})

  // 
  const [com, setcom] = useState({});
  //  
  const [size, setSize] = useState()
  // 
  const [refundData, setRefundData] = useState()
  //  
  const [isShowRefund, setIsShowRefund] = useState(false)
  // 
  const [refundParams, setRefundParams] = useState({
    host_id: '',
    suspend_reason: [],
    type: ""
  })

  // 
  const getReason = (e) => {
    setRefundParams({ ...refundParams, suspend_reason: e })
  }
  // 
  const getSelfReason = (e) => {
    setRefundParams({ ...refundParams, suspend_reason: e.target.value })
  }
  //  
  const getType = (e) => {
    setRefundParams({ ...refundParams, type: e })
  }
  // 
  const doRefund = () => {
    let isPass = true
    if (refundParams.suspend_reason.length === 0) {
      showDialog((refundData.reason_custom === 1 ? t('input') : t('select')) + t('common.stop_reason'), "error")
      return
    }
    if (!refundParams.type) {
      showDialog(t('select') + t('common.stop_time'), "error")
      return
    }
    if (isPass) {
      postRefund(refundParams).then(res => {
        if (res.data.status === 200) {
          showDialog("", "success")
          setIsShowRefund(false)
        }
      })
    }
  }
  // 
  const deleteItem = (record) => {
    // id
    const id = record.id
    setSize(record.size)
    // 
    setRefundParams({
      host_id: id,
      suspend_reason: [],
      type: ""
    })

    // const params = {
    //   id: Number(id)
    // }
    // 1  
    refundMsg(Number(id)).then(res => {
      if (res.data.data.refund) {
        showDialog("", "warning")
      } else {
        // 2 
        refund({ host_id: id }).then(res => {
          let list = res.data.data
          setRefundData(list)
          if (list.allow_refund === 1) {  // 
            // 
            setIsShowRefund(true)
          } else {
            showDialog(t('common.tip1'), "warning")
          }
        })
      }
    })
  }


  // 
  const unmountItem = (record) => {
    setModalTitle(`${record.name}`)
    setModalText(`
      `)
    setModalData({ id: record.id })
    setIsModalVisible(true)
  }




  // 
  const [isShowModal, setIsShowModal] = useState(false)
  const [isSelectLoading, setIsSelectLoading] = useState(false)
  const [diskId, setDiskId] = useState()
  // 
  const [errMsg, setErrMsg] = useState("")
  const [diskList, setDiskList] = useState([])
  // 
  const [isTableLoading, setIsTableLoading] = useState(false)
  // 
  const [datasource, setDatasource] = useState()
  const changePage = (page, limit) => {
    setParams(params => {
      return { ...params, page, limit }
    })
  }

  //  
  const [isDisk, setIsDisk] = useState(false)
  const columns = [
    {
      title: '',
      dataIndex: 'name',
      width: 700,
    },
    {
      title: '',
      dataIndex: 'create_time',
      width: 300,
      render: (create_time) => (
        <>
          {
            create_time ?
              moment(create_time * 1000).format("YYYY-MM-DD HH:mm")
              :
              "--"
          }
        </>
      )
    },
    {
      title: '',
      dataIndex: 'size',
      width: 200,
      render: (size) => (
        <span>{size}G</span>
      )
    },
    {
      title: "",
      width: 80,
      render: (_, record) => (
        <>
          {
            record.status == 'Active' ?
              <div className="com-opt-pop" id={'com-opt' + record.id}>
                <Popover getPopupContainer={() => document.getElementById('com-opt' + record.id)}
                  content={
                    <>
                      {
                        record.cloud_name ?
                          <p className="cloud-opt" onClick={() => uninDisk(record)}></p>
                          :
                          null
                        // <p className="cloud-opt" onClick={() => mountItem(record)}></p>
                      }
                      <p className="cloud-opt" onClick={() => expansionItem(record)}></p>
                      <p className="cloud-opt" onClick={() => deleteItem(record)}></p>
                    </>
                  } placement="topRight">
                  <p className="opt-icon"><EllipsisOutlined /></p>
                </Popover>
              </div>
              : null
          }
        </>
      )
    }
  ]


  // 
  const getCommonData = () => {
    getCommon().then((res) => {
      if (res.data.status === 200) {
        setcom(res.data.data);
      }
    });
  }

  // 
  const expansionItem = (record) => {
    navigate(`/expansion_storage?id=${record.id}&package_id=${record.package_id}&size=${record.size}`)
    // 
  }

  // 
  const getDiskList = () => {
    setIsTableLoading(true)
    const id = Number(window.location.hash.split("=")[1])
    getDisk({ ...params, host_id: id }).then(res => {
      console.log(res);
      if (res.data.data.list[0]) {
        setIsDisk(true)
        const list = res.data.data.list
        setTotal(res.data.data.count)
        setDatasource(list)
      } else {
        setIsDisk(false)
      }
      setIsTableLoading(false)
    })
  }

  // 
  const showMount = () => {
    // 
    navigate('/create_storage')
    // setIsShowModal(true)
  }
  // id
  const showDisk = (open) => {
    if (open) {
      setIsSelectLoading(true)
      const id = Number(window.location.hash.split("=")[1])
      getDiskById(id).then(res => {
        if (res.data.list) {
          setDiskList(res.data.list)
        }
        setIsSelectLoading(false)
      })
    }
  }

  const getDiskIdValue = (e) => {
    setDiskId(e)
  };

  //  
  const closeDialog = () => {
    setIsModalVisible(false)
  }


  // 
  const doCreateSnapshot = () => {
    const id = Number(window.location.hash.split("=")[1])
    const params = {
      id: diskId,
      host_id: id
    }
    console.log(params);
    let isPass = true
    if (!diskId) {
      isPass = false
      setErrMsg("")
    }

    if (isPass) {
      setErrMsg("")
      mount(params).then(res => {
        if (res.data.status === 200) {
          showDialog("", "success")
          setIsShowModal(false)
        }
      }).catch(error => {

      })
    }

  }

  // 
  const uninDisk = (record) => {
    Swal.fire({
      text: ` ${record.name},`,
      icon: 'info',
      showCancelButton: true,
      confirmButtonColor: '#5C5EE4',
      confirmButtonText: '',
      cancelButtonText: '',
      customClass: {
        icon: 'ssh-icon',
        cancelButton: 'ssh-cancel-button',
      }
    }).then((result) => {
      if (result.value) {
        // 
        umount(record.id).then(res => {
          if (res.data.status === 200) {
            showDialog(` ${record.name}`, "success")
            getDiskList()
          }
          console.log(res);
        })
      }
    })
  }


  useEffect(() => {
    getCommonData()
  }, [])

  useEffect(() => {
    getDiskList()
  }, [params]);

  return (
    <div className='cloud-card disk'>
      {
        isDisk ?
          // 
          <div className="disk-yes">
            <div className="main_table">
              <Table columns={columns}
                dataSource={datasource}
                pagination={false}
                loading={isTableLoading}
                rowKey={record => record.id}
              />
            </div>
            {/*  */}
            <ComPagination total={total} page={params} changePage={changePage}></ComPagination>
          </div>
          :
          // 
          <div className="disk-no">
            <img className='disk-no-img' src={require('@/assets/images/cloud/disk.png')} alt="" />
            <span className='disk-no-text'></span>
            <Button className='disk-no-btn' onClick={showMount}></Button>
          </div>
      }

      {/*  */}
      <Modal
        visible={isShowModal}
        footer={null}
        centered={true}
        closable={false}
        style={{ borderRadius: "10px", overflow: "auto" }}
        getContainer={false}
        onCancel={() => setIsShowModal(false)}
      >
        <div className='create-snapshot'>
          <div className="top">
            <div className="title">
              
            </div>
          </div>
          <div className="main">
            {
              errMsg ?
                <div className="err">
                  <Alert description={errMsg} type="error" showIcon />
                </div>
                :
                null
            }
            <div className="form">
              <div className="row">
                <Row>
                  <Col span={4}>
                    <span className='label'></span>
                  </Col>
                  <Col span={20}>
                    <Select loading={isSelectLoading} size='large' onDropdownVisibleChange={(open) => showDisk(open)} onChange={getDiskIdValue} >
                      {
                        diskList.map(item => {
                          return <Option key={item.id} value={item.id}>{`${item.name}-${item.size}G`}</Option>
                        })
                      }
                    </Select>
                  </Col>
                </Row>
              </div>
            </div>
          </div>
          <div className="footer">
            <Button className='cancel-btn' onClick={() => setIsShowModal(false)}></Button>
            <Button className='sub-btn' onClick={doCreateSnapshot}></Button>
          </div>
        </div>
      </Modal>

      {/*  */}
      {
        com && refundData ?
          <Modal
            className='refund-modal'
            visible={isShowRefund}
            footer={null}
            centered={true}
            closable={false}
            style={{ borderRadius: "10px", overflow: "auto" }}
            getContainer={false}
            onCancel={() => setIsShowRefund(false)}
          >
            <div className="modal-unopen">
              <div className="modal-title">{t('common.apply_refund')}</div>
              <div className="modal-form">
                {/*  */}
                <div className="config-item">
                  <div className="row-l">{t('common.product_info')}</div>
                  <div className="row-r">
                    {/*  */}
                    <div className="item">
                      <p className="l-tit">{t('common.product_config')}</p>
                      <div className="r-text">
                        {size ? size + 'G' : null}
                      </div>
                    </div>
                    {/*  */}
                    <div className="item">
                      <p className='l-tit'>{t('common.buy_time')}</p>
                      <div className="r-text">
                        <p>{moment(refundData.host.create_time * 1000).format("YYYY-MM-DD HH:mm")}</p>
                      </div>
                    </div>
                    {/*  */}
                    <div className="item">
                      <p className='l-tit'>{t('common.buy_money')}</p>
                      <div className="r-text">
                        <p>{com.currency_prefix}{refundData.host.first_payment_amount}{com.currency_code}</p>
                      </div>
                    </div>
                  </div>
                </div>
                {/*  */}
                <div className="config-item">
                  <div className="row-l">{t('common.stop_reason')}</div>
                  <div className="row-r">
                    {
                      refundData.reason_custom !== 1 ?
                        <Select mode="multiple" showArrow allowClear size='large' style={{ width: "100%" }} onChange={getReason}>
                          {
                            refundData.reasons.map(item => {
                              return <Option key={item.id} value={item.id}>{item.content}</Option>
                            })
                          }
                        </Select>
                        :
                        <TextArea onChange={getSelfReason} rows={4} placeholder="" />
                    }
                  </div>
                </div>
                {/*  */}
                <div className="config-item">
                  <div className="row-l row-center">
                    {t('common.stop_time')}
                  </div>
                  <div className="row-r">
                    <Select size='large' style={{ width: "100%" }} onChange={getType}>
                      <Option value="Expire">{t('common.expire')}</Option>
                      <Option value="Immediate">{t('common.immediate')}</Option>
                    </Select>
                  </div>
                </div>
                {/*  */}
                <div className="config-item money">
                  <div className="row-l">
                    {t('common.refund_amount')}
                  </div>
                  <div className="row-right">

                    {
                      refundParams.type === "Immediate" ?
                        // 
                        com.currency_prefix + refundData.host.amount + com.currency_code
                        :
                        com.currency_prefix + '0' + com.currency_code
                      // 
                    }
                  </div>
                </div>
              </div>
              <div className="modal-footer">
                <Button className='sure' onClick={doRefund}>
                  {t('common.sure_refund')}
                </Button>
                <Button className='cancel' onClick={() => setIsShowRefund(false)}>{t('common.cancel')}</Button>
              </div>
            </div>
          </Modal>
          : null
      }

      {/* / */}
      {/* {
        isModalVisible && <DelDialog
          closeDialog={closeDialog} submitDel={submitDel}
          text={modalText}
          title={modalTitle}
        ></DelDialog>
      } */}
    </div>
  )
}
