import React, { useState, useEffect, useRef } from "react";

import { useSearchParams } from "react-router-dom";
import { message } from "antd";

import * as cloud from "@/api/cloud";
import * as manage from "@/api/cloudManage";
import "./cloud.css";

import ComTab from "./components/com-tab/ComTab";
import Summary from "./summary/Summary";
import Charts from "./charts/Charts";
import Manage from "./manage/Manage";
import Disk from "./disk/Disk";
import Network from "./network/Network";
import Upgrade from "./upgrade/Upgrade";
import BackupSnapshot from "./backup_snapshot/BackupSnapshot";
import Log from "./log/Log";
export default function CloudDetail () {
  const [search] = useSearchParams();
  const id = search.get("id");
  /*  */
  const [detail, setDetail] = useState(null);
  const [host, setHost] = useState(null)
  const [loading, setLoading] = useState(true);
  const [activeTab, setActiveTab] = useState("summary");
  const [type, setType] = useState(null);
  const [isEscue, setIsEscue] = useState(0)
  let timer = useRef()
  useEffect(() => {
    getDetail();
    pollingStatus();
    getHostDetail()
    getIsRescue();
    return () => clearInterval(timer.current)
  }, []);
  /*  */
  // 
  let test = {};
  const getDetail = async () => {
    try {
      const res = await cloud.getCloudDetail(id);
      setDetail(res.data.data);
    } catch (error) {
      console.log(error)
      message.error(error.data.msg);
    }
  };
  // 
  const getHostDetail = async () => {
    try {
      const res = await manage.getProductDetail(id)
      setHost(res.data.data.host)
    } catch (error) {
      message.error(error.data.msg);
    }
  }
  // 
  const getIsRescue = async () => {
    try {
      const res = await manage.getIsRescue(id)
      if (res.data.status === 200) {
        setIsEscue(res.data.data.rescue)
      }
    } catch (error) {
      console.log(error)
    }
  }
  // 
  const pollingStatus = () => {
    timer.current = setInterval(async () => {
      setLoading(true);
      try {
        const res = await manage.getStatus(id);
        if (res.data.data.status !== "operating") {
          clearInterval(timer.current);
          setType(res.data.data.status);
          getIsRescue()
          setLoading(false);
          getDetail()
          return false;
        }
      } catch (error) {

      }
    }, 2000);
  };
  // 
  const refresh = () => {
    pollingStatus();
  };
  // 
  const refreshDetail = ()=>{
    getDetail()
  }
  const changeTab = (e) => {
    setActiveTab(e);
  };
  switch (activeTab) {
    case "summary":
      return (
        detail && (
          <div className="com-content cloud-detail">
            <ComTab
              activeTab={activeTab}
              changeTab={changeTab}
              detail={detail}
              host={host}
              loading={loading}
              type={type}
              refresh={refresh}
            ></ComTab>
            <Summary></Summary>
          </div>
        )
      );
    case "charts":
      return (
        detail && (
          <div className="com-content cloud-detail">
            <ComTab
              activeTab={activeTab}
              changeTab={changeTab}
              detail={detail}
              host={host}
              loading={loading}
              type={type}
              refresh={refresh}
            ></ComTab>
            <Charts id={id}></Charts>
          </div>
        )
      );
    case "manage":
      return (
        detail && (
          <div className="com-content cloud-detail">
            <ComTab
              activeTab={activeTab}
              changeTab={changeTab}
              detail={detail}
              host={host}
              loading={loading}
              type={type}
              refresh={refresh}
            ></ComTab>
            <Manage detail={detail} refresh={refresh} type={type} isEscue={isEscue}></Manage>
          </div>
        )

      );
    case "disk":
      return (
        detail && (
          <div className="com-content cloud-detail">
            <ComTab
              activeTab={activeTab}
              changeTab={changeTab}
              detail={detail}
              host={host}
              loading={loading}
              type={type}
              refresh={refresh}
            ></ComTab>
            <Disk></Disk>
          </div>
        )
      );
    case "network":
      return (
        detail && (
          <div className="com-content cloud-detail">
            <ComTab
              activeTab={activeTab}
              changeTab={changeTab}
              detail={detail}
              host={host}
              loading={loading}
              type={type}
              refresh={refresh}
            ></ComTab>
            <Network id={id} detail={detail} refreshDetail={refreshDetail}></Network>
          </div>
        )
      );
    case "upgrade":
      return (
        detail && (
          <div className="com-content cloud-detail">
            <ComTab
              activeTab={activeTab}
              changeTab={changeTab}
              detail={detail}
              host={host}
              loading={loading}
              type={type}
              refresh={refresh}
            ></ComTab>
            <Upgrade id={id} detail={detail} host={host} refreshDetail={refreshDetail}></Upgrade>
          </div>
        )
      );
    case "backup_snapshot":
      return (
        detail && (
          <div className="com-content cloud-detail">
            <ComTab
              activeTab={activeTab}
              changeTab={changeTab}
              detail={detail}
              host={host}
              loading={loading}
              type={type}
              refresh={refresh}
            ></ComTab>
            <BackupSnapshot></BackupSnapshot>
          </div>
        )
      );
    case "log":
      return (
        detail && (
          <div className="com-content cloud-detail">
            <ComTab
              activeTab={activeTab}
              changeTab={changeTab}
              detail={detail}
              host={host}
              loading={loading}
              type={type}
              refresh={refresh}
            ></ComTab>
            <Log id={id}></Log>
          </div>
        )
      );
    default:
      return null;
  }
  return null;
}
