import React, { useState, useEffect } from 'react';
import { useTranslation } from "react-i18next";
import { message, Table } from 'antd';
import moment from 'moment'

import * as cloud from '@/api/cloud';
import '../../cloud/cloud.css';

import ComPagination from '@/components/comPagination/ComPagination.jsx'
export default function Log(props) {
  const { t } = useTranslation()
  /*  */
  const [logList, setLogList] = useState([])
  const id = props.id
  const [total, setTotal] = useState(0)
  const [params, setParams] = useState({
    id,
    keywords: '',
    page: 1,
    limit: 20,
    orderby: 'id',
    sort: 'desc',
  })
  const columns = [
    {
      title: 'ID',
      dataIndex: 'id',
      key: 'id',
      width: 120
    },
    {
      title: t('cloud.description'),
      dataIndex: 'description',
      key: 'description'
    },
    {
      title: t("common.create_time"),
      dataIndex: 'due_time',
      key: 'due_time',
      width: 200,
      render: (_, record) => (
        <>
          <span>{moment(record.create_time * 1000).format('YYYY-MM-DD HH: mm')}</span>
        </>
      )
    },
    {
      title: 'IP',
      dataIndex: 'ip',
      key: 'ip',
      width: 150
    }
  ];
  /*  */
  const getLogList = async () => {
    try {
      const res = await cloud.getLog(params)
      setLogList(res.data.data.list)
      setTotal(res.data.data.count)
    } catch (error) {
      message.error(error.data.msg)
    }
  }
  // 
  const changePage = (page, size) => {
    setParams(params => {
      params.limit = size
      params.page = page
      return params
    })
    getLogList()
  }

  useEffect(() => {
    getLogList()
  }, [])
  return (
    <div className="com-content cloud-log">
      <div className="com-card">
        <Table columns={columns} dataSource={logList}
          rowKey={record => record.id} pagination={false} className="cloud-tab"
          scroll={{ x: 'max-content' }} />
        <ComPagination total={total} page={params} changePage={changePage} ></ComPagination>
      </div>
    </div>
  )
}
