import request from "../utils/request";

// 
export function order(param) {
  return request.get(`/order`, { params: { ...param } });
}

// 
export function transaction(param) {
  return request.get(`/transaction`, { params: { ...param } });
}

// 
export function orderDetails(param) {
  return request.get(`/order/${param}`);
}
// {id: id,gateway:}
export function onlinePay(params) {
  return request.post(`/pay`, params);
}

// 
export function account() {
  return request.get('/account')
}

// 
export function credit(param) {
  return request.get('/credit', { params: { ...param } })
}

// 
export function gateway() {
  return request.get('/gateway')
}

// {id:id,use:10}
export function creditPay(params) {
  return request.post(`/credit`, params);
}

// 
export function getPayStatus(id) {
  return request.get(`/pay/${id}/status`);
}

// id
export function recharge(param) {
  return request.post(`/recharge`, { ...param })
}

// 
export function pay(param) {
  return request.post(`/pay`, { ...param })
}

// 
export function withdrawRule(params) {
  return request.get(`withdraw/rule/credit`, {params});
}
// 
export function withdraw(param) {
  return request.post(`/withdraw`, { ...param })
}

// 
export function unSh() {
  return request.get(`/refund/pending/amount`)
}

// 
export function promoCode(id) {
  return request.get(`/promo_code/host/${id}/promo_code`)
}

