import React, { useState, useEffect, useReducer, useRef } from "react";
import { useTranslation } from "react-i18next";
import { useNavigate } from "react-router-dom";
import { Col, Row, Tabs, message, Button, Modal } from "antd";
import { getCommon } from "@/api/common.js"
import { showDialog } from '@/utils/util'
import * as cloud from "@/api/cloud";
import PayModal from "@/components/payModal/PayModal";
import "./upgrade.less";
import moment from 'moment'
export default function Upgrade (props) {
  const id = props.id;
  const navigate = useNavigate();
  const { t } = useTranslation();
  const { TabPane } = Tabs;
  const [plist, setPlist] = useState([]);
  const [ptab, setPtab] = useState([]);
  const [cur, setCur] = useState();
  const [curId, setCurId] = useState(0);
  const timer = useRef()
  // 
  const [curConfig, setCurConfig] = useState({})
  const [afterConfig, setAfterConfig] = useState({})
  // 
  const [time, setTime] = useState(300000); // 5
  // 
  const [common, setCommon] = useState('')
  const [orderId, setOrderId] = useState()
  const [isShowPay, setIsShowPay] = useState(false)
  // 
  const [upgradeData, setUpgradeData] = useState({})
  // modal
  const [isShowModal, setIsShowModal] = useState(false)
  // 
  const getCommonConfig = async () => {
    const res = await getCommon()
    setCommon(res.data.data)
  }
  // 
  const endDayCount = props.host ? Math.floor((props.host.due_time * 1000 - Date.now()) / (1000 * 60 * 60 * 24)) : '-'
  // id
  const packageId = props.detail.package.id
  const detail = props.detail
  const host = props.host
  useEffect(() => {
    getCommonConfig();
    getPackageList();
    const temp = {
      description: detail.package.description,
      price: host.first_payment_amount
    }
    setCurConfig(temp)
    return () => clearInterval(timer.current)
  }, []);

  const changePackage = (e) => {
    setCur(0);
    getPackageList(e);
  };
  // 
  const changePackageId = (id, index) => {
    setCur(index);
    setCurId(id)
    const temp = plist.filter(item => item.id === id)
    setAfterConfig(temp[0])
  };
  const getPackageList = async (groupId) => {
    try {
      const res = await cloud.getPackage({
        product_id: 30,
        cal_group_id: groupId,
        bw_type_id: detail.bw.id,
        data_center_id: detail.data_center.id,
      });
      const data = res.data.data;
      setPtab(data.cal_group);
      setPlist(data.package);
      // const temp = data.package.filter(item => item.id === packageId)
      // setCurConfig(temp[0])
    } catch (error) {
      console.log(error)
      message.error(error.data.msg);
    }
  };
  // 
  const submitUpgrade = () => {
    if (!curId || (curId === packageId)) {
      return showDialog(t('select') + t('common.package'), 'error')
    }
    getPackagePrice()
  }
  // 
  const getPackagePrice = async () => {
    try {
      const params = {
        id,
        package_id: curId
      }
      const res = await cloud.getPackagePrice(params)
      if (res.data.status === 200) {
        setUpgradeData(res.data.data)
        setIsShowModal(true)
      }
    } catch (error) {
    }
  }
  // 
  const createPackageId = async () => {
    try {
      const params = {
        id,
        package_id: curId
      }
      const res = await cloud.createPackageId(params)
      if (res.data.status === 200) {
        if (Number(upgradeData.price) <= 0) {
          showDialog(t('cloud.tip34'), 'success', () => {
            setIsShowModal(false)
            props.refreshDetail()
          })
        } else {
          setIsShowPay(true)
          setOrderId(res.data.data.id)
          // 
          pollingStatus(res.data.data.id)
        }
      }
    } catch (error) {
      console.log(error)
    }
  }
  // 
  const pollingStatus = (id) => {
    timer.current = setInterval(async () => {
      const res = await cloud.getPayStatus(id ? id : orderId);
      setTime((time) => {
        return (time -= 2000);
      });
      if (res.data.code === "Paid") {
        showDialog(res.data.msg, 'success', () => {
          // 
          setIsShowModal(false)
          props.refreshDetail()
        })
        clearInterval(timer.current);
        setIsShowPay(false);
        return false;
      }
      // 5
      if (res.code === "Paid" || time === 0) {
        clearInterval(timer.current);
        setIsShowPay(false);
        return false;
      }
    }, 2000)
  };
  // 
  const handleCancel = () => {
    setIsShowPay(false)
    clearInterval(timer.current);
  }
  return (
    <div className="com-content add-cloud cloud-upgrade">
      <div className="com-card">
        <div className="box">
          <div className="package">
            <Tabs onChange={changePackage}>
              {ptab?.map((item) => (
                <TabPane tab={item.name} key={item.id}>
                  <p className="info">
                    {item.description}
                  </p>
                  <Row gutter={{ xs: 8, md: 24, lg: 35 }}>
                    {plist.map((item, index) => {
                      return (
                        <Col
                          xs={{ span: 24 }}
                          md={{ span: 12 }}
                          lg={{ span: 6 }}
                          key={item.id}
                        >
                          <div
                            className={`item c-hover ${(curId === item.id) && (curId !== packageId) ? "c-active" : ""
                              } ${item.id === packageId ? "mask" : ""}`}
                            key={item.id}
                            onClick={() => changePackageId(item.id, index)}
                          >
                            <p className="price">
                              <span className="p-num">
                                {common.prefix}{item.price.split(".")[0]}
                              </span>
                              <span className="cycle">
                                .{item.price.split(".")[1]}/mo
                              </span>
                            </p>
                            <p className="des" dangerouslySetInnerHTML={{ __html: item.description }}></p>
                          </div>
                        </Col>
                      );
                    })}
                  </Row>
                  <div className="upbtn">
                    <Button
                      className="sub-button"
                      type="primary"
                      onClick={submitUpgrade}
                    >
                      {t('common.sure')}
                    </Button>
                  </div>
                </TabPane>
              ))}
            </Tabs>
          </div>
        </div>
      </div>
      {/*  */}
      {
        common && upgradeData ? <Modal
          className='refund-modal'
          visible={isShowModal}
          footer={null}
          centered={true}
          closable={false}
          style={{ borderRadius: "10px", overflow: "auto" }}
          maskClosable={true}
          keyboard={true}
          getContainer={false}
          onCancel={() => setIsShowModal(false)}
        >
          {
            isShowModal && <div className="modal-unopen">
              <div className="modal-title">{t('cloud.upgrade_cloud')}</div>
              <div className="modal-form">
                <div className="config-item">
                  <div className="row-l">{t('cloud.cur_config')}</div>
                  <div className="row-r">
                    <div className="item">
                      <p className="des" dangerouslySetInnerHTML={{ __html: curConfig?.description }}></p>
                      <p className="price">{common.currency_prefix}{curConfig?.price}/{t('cloud.month')}</p>
                    </div>
                  </div>
                </div>
                <div className="config-item">
                  <div className="row-l">{t('cloud.after_config')}</div>
                  <div className="row-r white">
                    <div className="item">
                      <p className="des" dangerouslySetInnerHTML={{ __html: afterConfig?.description }}></p>
                      <p className="price">{common.currency_prefix}{afterConfig?.price}/{t('cloud.month')}</p>
                    </div>
                  </div>
                </div>
                <div className="due-time">{t('cloud.cur_time')}
                  <span className="day">
                    {endDayCount}
                  </span>
                  {t('common.day')}</div>
                <div className="total-price">
                  {t('common.cur_cost')}
                  <div className="num"><span>{common.currency_prefix}</span>{upgradeData?.price}</div>
                </div>
              </div>
              <div className="modal-footer">
                <Button className='sure' onClick={createPackageId}>
                  {t('common.sure')}
                </Button>
                <Button className='cancel' onClick={() => setIsShowModal(false)}>{t('common.cancel')}</Button>
              </div>
            </div>
          }
        </Modal>
          : null
      }
      {/* modal */}
      {
        orderId &&
        (
          <PayModal orderId={orderId} timer={timer} visible={isShowPay} handleCancel={handleCancel}></PayModal>
        )
      }
    </div>
  );
}
