import { t } from 'i18next'
import React, { useEffect, useState, useRef } from 'react'
import { Tooltip } from 'antd';
import {
  SyncOutlined
} from '@ant-design/icons';
import PowerModal from '@/components/powerModal/PowerModal';
import { showDialog } from '@/utils/util.js'
import * as manage from '@/api/cloudManage'
export default function ComTab (props) {
  const change = (e) => {
    props.changeTab(e.target.getAttribute('data-name'))
  }
  const menu = [
    { name: 'summary', title: t('cloud.summary') },
    { name: 'charts', title: t('cloud.charts') },
    { name: 'manage', title: t('cloud.manage') },
    { name: 'disk', title: t('cloud.disk') },
    { name: 'network', title: t('cloud.network') },
    { name: 'upgrade', title: t('cloud.upgrade') },
    { name: 'backup_snapshot', title: t('cloud.backup_snapshot') },
    { name: 'log', title: t('cloud.log') }
  ]
  const detail = props.detail
  const host = props.host
  console.log("host",host);
  const [info, setInfo] = useState({})
  const [optTitle, setOptTitle] = useState('')
  const [optVisible, setOptVisible] = useState(false)
  const [optType, setOptType] = useState('')
  const timer = useRef()
  // 
  const powerStatus = (e) => {
    switch (e) {
      case 'on':
        return t('cloud.on')
      case 'off':
        return t('cloud.off')
      case 'operating':
        return t('cloud.operating')
      case 'suspend':
        return t('cloud.Suspended')
      case 'fault':
        return t('cloud.fault')
      default:
        return 'Loading...'
    }
  }
  const editItem = (type, row) => {
    setOptVisible(true)
    switch (type) {
      case 'on':
        return setOptTitle(t('cloud.tip16'))
      case 'off':
        return setOptTitle(t('cloud.tip17'))
      case 'reboot':
        return setOptTitle(t('cloud.tip18'))
      default:
        return setOptTitle('title')
    }
  }
  useEffect(() => {
  }, [])
  const optCancel = () => {
    setOptVisible(false)
  }
  const onFinish = (force) => {
    switch (optType) {
      case 'on':
        return powerOn(force)
      case 'off':
        return powerOff(force)
      case 'reboot':
        return reboot(force)
      default:
        break;
    }
  }
  /*  */
  // 
  const refresh = () => {
    props.refresh()
  }
  // 
  const showDia = (msg, status) => {
    showDialog(msg, status, () => {
      optCancel()
    })
  }
  // 
  const handlerClose = () => {
    if (!props.type) {
      return
    }
    if (props.type === 'on') {
      setOptTitle(t('cloud.tip17'))
      setOptType('off')
    } else if (props.type === 'off') {
      setOptTitle(t('cloud.tip16'))
      setOptType('on')
    }
    setOptVisible(true)
  }
  // /
  const handlerRestart = () => {
    if (!props.type) {
      return
    }
    setOptTitle(t('cloud.tip18'))
    setOptType('reboot')
    setOptVisible(true)
  }
  // 
  const handlerConsole = () => {
    if (!props.type) {
      return
    }
    postVnc()
  }
  // URL
  const postVnc = async () => {
    try {
      const res = await manage.postVnc(host.id)
      window.open(res.data.data.url)
    } catch (error) {
    }
  }
  // 
  const powerOn = async () => {
    try {
      const res = await manage.powerOn(host.id)
      if (res.data.status === 200) {
        refresh()
        showDia(res.data.msg, 'success')
      }
    } catch (error) {
      showDia(error.data.msg, 'error')
    }
  }
  // 
  const powerOff = async (force) => {
    try {
      const res = await manage.powerOff(force, host.id)
      if (res.data.status === 200) {
        refresh()
        showDia(res.data.msg, 'success')
      }
    } catch (error) {
      showDia(error.data.msg, 'error')
    }
  }
  // 
  const reboot = async (force) => {
    try {
      const res = await manage.reboot(force, host.id)
      if (res.data.status === 200) {
        refresh()
        showDia(res.data.msg, 'success')
      }
    } catch (error) {
      showDia(error.data.msg, 'error')
    }
  }
  return (
    <>
      {
        detail && <div className='cloud-card'>
          <div className="t-info">
            <div className="l-info">
              <p className="tit">{host.product_name}</p>
              <p>
                <img src={detail.data_center.country_code && require(`@/assets/images/cloud/${detail.data_center.country_code}.png`)}
                  alt="" className='country_code' />
                <span>{detail.data_center.city}</span>
              </p>
            </div>
            <div className="r-info">
              <Tooltip placement="top" title={powerStatus(props.type)}
                getPopupContainer={() => document.getElementById('status')}>
                <span id="status">
                  {
                    props.loading ? <SyncOutlined spin /> : <img src={require(`@/assets/images/cloud/${props.type}.png`)} alt="" />
                  }
                </span>
              </Tooltip>
              <img src="/" alt="" className="os" />
              <span>{detail.image.name}</span>
              <div className="opt">
                <img src={require(`@/assets/images/cloud/close.png`)} alt="" onClick={handlerClose} />
                <img src={require(`@/assets/images/cloud/restart.png`)} alt="" onClick={handlerRestart} />
                <img src={require(`@/assets/images/cloud/console.png`)} alt="" onClick={handlerConsole} />
              </div>
            </div>
          </div>
          <div className="tabs">
            {
              menu.map(item => {
                return (
                  <span data-name={item.name} onClick={change} key={item.name}
                    className={`${props.activeTab === item.name ? 'active' : ''}`}>
                    {item.title}
                  </span>
                )
              })
            }
          </div>
        </div>
      }
      {
        optVisible && <PowerModal info={host} optTitle={optTitle} optType={optType}
          optCancel={optCancel} onFinish={onFinish}
        ></PowerModal>
      }
    </>

  )
}
