/*  */
import request from "../utils/request";

// 
export function getDataCenter (id) {
  return request.get(`/product/${id}/idcsmart_cloud/data_center`);
}
// 
export function getBwType (id) {
  return request.get(`/product/${id}/idcsmart_cloud/bw_type`);
}
// 
export function getPackage (params) {
  return request.get(`/product/${params.product_id}/idcsmart_cloud/package`, {
    params,
  });
}
// 
export function getSystem (params) {
  return request.get(
    `/product/${params.product_id}/idcsmart_cloud/system_image`,
    { params }
  );
}
// vpc
export function getVpc (params) {
  return request.get(`/vpc`, { params });
}
// 
export function getGroup (params) {
  return request.get(`/security_group`, { params });
}
