export function getDurationPprice (params) {
  return request.get(`/idcsmart_cloud/duration_price`, { params });
}
// package_id,image_id,backup_enable,panel_enable
export function updatePrice (params) {
  return request.post(`/product/${params.product_id}/config_option`, params);
}

// 
export function settleProduct (params) {
  return request.post(`/product/settle`, params);
}

