import React, { useState, useEffect, useReducer, useRef } from "react";
import { useTranslation } from "react-i18next";
import moment from "moment";
import * as cloud from "@/api/cloud";
import { Col, Row, message } from "antd";
import * as echarts from "echarts";
import "./chart.less";
export default function Charts(props) {
  const id = props.id;
  const { t } = useTranslation();
  const [cputype, setcputype] = useState(1);
  const [iotype, setiotype] = useState(1);
  const [metype, setmetype] = useState(1);
  const [nettype, setnettype] = useState(1);
  // const [cpulist, setcpulist] = useState([]);
  const changetype = (e) => {
    setcputype(e);
  };
  const changetype2 = (e) => {
    setiotype(e);
  };
  const changetype3 = (e) => {
    setmetype(e);
  };
  const changetype4 = (e) => {
    setnettype(e);
  };
  // useEffect(() => {
  //   getList("cpu", cputype);

  // }, []);
  useEffect(() => {
    getList("cpu", cputype);
  }, [cputype]);

  useEffect(() => {
    getList("disk_io", iotype);
  }, [iotype]);

  useEffect(() => {
    getList("memory", metype);
  }, [metype]);

  useEffect(() => {
    getList("bw", nettype);
  }, [nettype]);
  //
  const getstarttime = (type) => {
    console.log(parseInt(new Date().getTime() / 1000), "time");
    let nowtime = parseInt(new Date().getTime() / 1000);
    if (type === 1) {
      return nowtime - 24 * 60 * 60;
    } else if (type === 2) {
      return nowtime - 24 * 60 * 60 * 3;
    } else if (type === 3) {
      return nowtime - 24 * 60 * 60 * 7;
    } else if (type === 4) {
      return nowtime - 24 * 60 * 60 * 30;
    } else if (type === 5) {
      return nowtime - 24 * 60 * 60 * 30 * 6;
    }
  };
  //cpu
  const getList = async (type, timetype) => {
    let start_time = getstarttime(timetype);
    console.log(start_time, "getstarttime");
    let parmar = {
      id,
      type,
      start_time,
    };
    try {
      const res = await cloud.chartdata(parmar);
      const list = res.data.data.list;
      let xAxis = [];
      let yAxis = [];
      let yAxis2 = [];
      let yAxis3 = [];
      let yAxis4 = [];
      list.map((item) => {
        if (type === "cpu") {
          xAxis.push(moment(item.time * 1000).format("YYYY-MM-DD HH:mm"));
          yAxis.push((item.value * 100).toFixed(2));
          getcpuchart(xAxis, yAxis);
        }
        if (type === "disk_io") {
          xAxis.push(moment(item.time * 1000).format("YYYY-MM-DD HH:mm"));
          yAxis.push((item.read_bytes / 1024 / 1024).toFixed(2));
          yAxis2.push((item.read_iops / 1024 / 1024).toFixed(2));
          yAxis3.push(item.write_bytes.toFixed(2));
          yAxis4.push(item.write_iops.toFixed(2));
          getioChart(xAxis, yAxis, yAxis2, yAxis3, yAxis4);
        }
        if (type === "memory") {
          xAxis.push(moment(item.time * 1000).format("YYYY-MM-DD HH:mm"));
          yAxis.push((item.total / 1024 / 1024).toFixed(2));
          yAxis2.push((item.used / 1024 / 1024).toFixed(2));
          getmemoryChart(xAxis, yAxis, yAxis2);
        }
        if (type === "bw") {
          xAxis.push(moment(item.time * 1000).format("YYYY-MM-DD HH:mm"));
          yAxis.push(item.in_bw.toFixed(2));
          yAxis2.push(item.out_bw.toFixed(2));
          getbwchart(xAxis, yAxis, yAxis2);
        }
      });

      console.log(xAxis, yAxis, "data");
    } catch (error) {
      message.error(error.data.msg);
    }
  };
  //
  const getmemoryChart = (xAxis, yAxis, yAxis2) => {
    var chartDom = document.getElementById("memorychart");
    var myChart = echarts.init(chartDom);
    // console.log(myChart, "myChart1");
    var option;
    option = {
      tooltip: {
        show: true,
        trigger: "axis",
      },
      xAxis: {
        type: "category",
        boundaryGap: false,
        data: xAxis,
      },
      yAxis: {
        // name: "/M",
        type: "value",
      },
      series: [
        {
          name: "(MB)",
          data: yAxis,
          type: "line",
          areaStyle: {},
        },
        {
          name: "(MB)",
          data: yAxis2,
          type: "line",
          areaStyle: {},
        },
      ],
    };
    option && myChart.setOption(option);
  };
  //IO
  const getioChart = (xAxis, yAxis, yAxis2, yAxis3, yAxis4) => {
    var chartDom = document.getElementById("iochart");
    var myChart = echarts.init(chartDom);
    // console.log(myChart, "myChart1");
    var option;
    option = {
      tooltip: {
        show: true,
        trigger: "axis",
      },
      xAxis: {
        type: "category",
        boundaryGap: false,
        data: xAxis,
      },
      yAxis: {
        // name: "B/s",
        type: "value",
      },
      series: [
        {
          name: "(MB/s)",
          data: yAxis,
          type: "line",
          areaStyle: {},
        },
        {
          name: "(MB/s)",
          data: yAxis2,
          type: "line",
          areaStyle: {},
        },
        {
          name: "IOPS",
          data: yAxis3,
          type: "line",
          areaStyle: {},
        },
        {
          name: "IOPS",
          data: yAxis4,
          type: "line",
          areaStyle: {},
        },
      ],
    };
    option && myChart.setOption(option);
  };
  //CPU
  const getcpuchart = (xAxis, yAxis) => {
    var chartDom = document.getElementById("cpuchart");
    var myChart = echarts.init(chartDom);
    // console.log(myChart, "myChart1");
    var option;
    option = {
      tooltip: {
        show: true,
        trigger: "axis",
      },
      xAxis: {
        type: "category",
        boundaryGap: false,
        data: xAxis,
      },
      yAxis: {
        type: "value",
      },
      series: [
        {
          name: "%",
          data: yAxis,
          type: "line",
          areaStyle: {},
        },
      ],
    };
    option && myChart.setOption(option);
  };
  //
  const getbwchart = (xAxis, yAxis, yAxis2) => {
    var chartDom = document.getElementById("bwchart");
    var myChart = echarts.init(chartDom);
    // console.log(myChart, "myChart1");
    var option;
    option = {
      tooltip: {
        show: true,
        trigger: "axis",
      },
      xAxis: {
        type: "category",
        boundaryGap: false,
        data: xAxis,
      },
      yAxis: {
        type: "value",
      },
      series: [
        {
          name: "(bps)",
          data: yAxis,
          type: "line",
          areaStyle: {},
        },
        {
          name: "(bps)",
          data: yAxis2,
          type: "line",
          areaStyle: {},
        },
      ],
    };
    option && myChart.setOption(option);
  };
  return (
    <div className="chart">
      <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 30 }}>
        <Col xs={{ span: 24 }} lg={{ span: 12 }}>
          <div className="cloud-card chartbox">
            <p className="chart_title">CPU</p>
            <div className="chart_type">
              <div
                className={`chart_type_item ${cputype === 1 ? "active" : ""}`}
                onClick={() => changetype(1)}
              >
                24
              </div>
              <div
                className={`chart_type_item ${cputype === 2 ? "active" : ""}`}
                onClick={() => changetype(2)}
              >
                3
              </div>
              <div
                className={`chart_type_item ${cputype === 3 ? "active" : ""}`}
                onClick={() => changetype(3)}
              >
                7
              </div>
              <div
                className={`chart_type_item ${cputype === 4 ? "active" : ""}`}
                onClick={() => changetype(4)}
              >
                1
              </div>
              <div
                className={`chart_type_item ${cputype === 5 ? "active" : ""}`}
                onClick={() => changetype(5)}
              >
                
              </div>
              <div id="cpu_chart"></div>
            </div>
            <div id="cpuchart" className="echarts"></div>
          </div>
        </Col>
        <Col xs={{ span: 24 }} lg={{ span: 12 }}>
          <div className="cloud-card chartbox">
            <p className="chart_title">IO</p>
            <div className="chart_type">
              <div
                className={`chart_type_item ${iotype === 1 ? "active" : ""}`}
                onClick={() => changetype2(1)}
              >
                24
              </div>
              <div
                className={`chart_type_item ${iotype === 2 ? "active" : ""}`}
                onClick={() => changetype2(2)}
              >
                3
              </div>
              <div
                className={`chart_type_item ${iotype === 3 ? "active" : ""}`}
                onClick={() => changetype2(3)}
              >
                7
              </div>
              <div
                className={`chart_type_item ${iotype === 4 ? "active" : ""}`}
                onClick={() => changetype2(4)}
              >
                1
              </div>
              <div
                className={`chart_type_item ${iotype === 5 ? "active" : ""}`}
                onClick={() => changetype2(5)}
              >
                
              </div>
            </div>
            <div id="iochart" className="echarts"></div>
          </div>
        </Col>
        <Col xs={{ span: 24 }} lg={{ span: 12 }}>
          <div className="cloud-card chartbox">
            <p className="chart_title"></p>
            <div className="chart_type">
              <div
                className={`chart_type_item ${metype === 1 ? "active" : ""}`}
                onClick={() => changetype3(1)}
              >
                24
              </div>
              <div
                className={`chart_type_item ${metype === 2 ? "active" : ""}`}
                onClick={() => changetype3(2)}
              >
                3
              </div>
              <div
                className={`chart_type_item ${metype === 3 ? "active" : ""}`}
                onClick={() => changetype3(3)}
              >
                7
              </div>
              <div
                className={`chart_type_item ${metype === 4 ? "active" : ""}`}
                onClick={() => changetype3(4)}
              >
                1
              </div>
              <div
                className={`chart_type_item ${metype === 5 ? "active" : ""}`}
                onClick={() => changetype3(5)}
              >
                
              </div>
            </div>
            <div id="memorychart" className="echarts"></div>
          </div>
        </Col>
        <Col xs={{ span: 24 }} lg={{ span: 12 }}>
          <div className="cloud-card chartbox">
            <p className="chart_title"></p>
            <div className="chart_type">
              <div
                className={`chart_type_item ${nettype === 1 ? "active" : ""}`}
                onClick={() => changetype4(1)}
              >
                24
              </div>
              <div
                className={`chart_type_item ${nettype === 2 ? "active" : ""}`}
                onClick={() => changetype4(2)}
              >
                3
              </div>
              <div
                className={`chart_type_item ${nettype === 3 ? "active" : ""}`}
                onClick={() => changetype4(3)}
              >
                7
              </div>
              <div
                className={`chart_type_item ${nettype === 4 ? "active" : ""}`}
                onClick={() => changetype4(4)}
              >
                1
              </div>
              <div
                className={`chart_type_item ${nettype === 5 ? "active" : ""}`}
                onClick={() => changetype4(5)}
              >
                
              </div>
            </div>
            <div id="bwchart" className="echarts"></div>
          </div>
        </Col>
      </Row>
    </div>
  );
}
